import { Component } from "@angular/core";
import { MatDialogRef } from "@angular/material";


@Component({
  templateUrl: "new-resource-dialog.component.html",
  styleUrls: ["new-resource-dialog.component.scss"]
})
export class NewResourceDialogComponent {
  showFileUpload: boolean;
  showWebURL: boolean;
  resourceTypevalue: string;
  constructor(public dialogRef:MatDialogRef<NewResourceDialogComponent>){

  }

  onResourceType(event: any) {
    this.resourceTypevalue = event.target.value;
    if (this.resourceTypevalue === "1") {
      this.showFileUpload = true;
      this.showWebURL = false;
    } else {
      this.showFileUpload = false;
      this.showWebURL = true;
    }
  }

  closeDialog() {
    this.dialogRef.close();
  }
}
