import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtree";
@Component({
  selector: "manage-prerequisite-mapping",
  templateUrl: "./manage-prerequisite-mapping.component.html",
  styleUrls: ["./manage-prerequisite-mapping.component.scss"]
})
export class ManagePrerequisiteMappingComponent {
  
  syllabusSource: any[] = [
    {
      label: "Maths",
      expanded: true,
      items: [
        {
          label: "Algebra",
          expanded: true,
          items: [
            {
              label: "Basic Fundamentals",
              selected: true
            },
            {
              label: "Introduction",
              selected: true
            }
          ]
        },
        {
          label: "Trignometry",
          expanded: true,
          items: [
            {
              label: "Basics",
              selected: true
            },
            {
              label: "Sine and Cosine degree",
              selected: true
            }
          ]
        }
      ]
    }
  ];

  showPrequisiteStandard: boolean;
  
  @ViewChild("treeReference")
  treeReference: jqxTreeComponent;

  constructor(private routes: Router) {}


  myTreePreviewSelect(event: any): void {
    let args = event.args;
    let item = this.treeReference.getItem(args.element);
    if (item.hasItems == false) {
      this.showPrequisiteStandard = true;
    } else {
      this.showPrequisiteStandard = false;
    }
  }

  openNewSyllabus() {
    this.routes.navigateByUrl("/manage-prerequisite-standard-mapping/create-new");
  }
}
