import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";
import { MatDialog, MatDialogRef } from "@angular/material";
import { SubjectNewComponent } from "./subject-new/subject-new.component";
import { PublishSubjectComponent } from "./publish-subject/publish-subject.component";

@Component({
  selector: "manage-subjects",
  templateUrl: "./manage-subjects.component.html",
  styleUrls: ["./manage-subjects.component.scss"]
})
export class ManageSubjectsComponent {
  dialogRef: MatDialogRef<PublishSubjectComponent>;

  constructor(private dialog: MatDialog, private routes: Router) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(70vh)";
  }

  data: any[] = [
    {
      subject: "Mathematics",
      description: "Probability ..",
      status: "draft"
    },
    {
      subject: "Science",
      description: "Measurements ..",
      status: "published"
    },
    {
      subject: "Mathematics",
      description: "Probability ..",
      status: "draft"
    }
  ];
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "subject", type: "string" },
      { name: "description", type: "string" },
      { name: "status", type: "string" }
    ],
    localData: this.data
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Subject Title",
      dataField: "subject",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Description",
      dataField: "description",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Status",
      dataField: "status",
      align: "center",
      cellsAlign: "center",
      width: 90
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      width: 200,
      columnType: "none",
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (
        row: number,
        column: any,
        value: any,
        rowData
      ): string => {
        return rowData.status === "draft" && rowData.status !== null
          ? `<div data-row='` +
              row +
              `' class='viewButton' style='color:white;background-color:purple;margin:0 5px;float: left;margin-right: 5px;'></div>
        <div data-row='` +
              row +
              `' class='publishButton' style='color:white;background-color:green;margin:0 5px;float:left'></div>`
          : `<div data-row='` +
              row +
              `' class='viewButton' style='color:white;background-color:purple;margin:0 5px;float: left;margin-right: 5px;'></div>
        <div data-row='` +
              row +
              `' class='unpublishButton' style='color:white;background-color:red;margin:0 5px;float:left;display:none'></div> <div data-row='` +
              row +
              `' class='unpublishButton' style='color:white;background-color:red;margin:0 5px;float:left'></div>`;
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyviewButtons = jqwidgets.createInstance(".viewButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "View"
    });

    let uglypublishButtons = jqwidgets.createInstance(
      ".publishButton",
      "jqxButton",
      {
        width: 80,
        height: 24,
        value: "Publish"
      }
    );
    let uglyunpublishButtons = jqwidgets.createInstance(
      ".unpublishButton",
      "jqxButton",
      {
        width: 80,
        height: 24,
        value: "Unpublish"
      }
    );

    let flattenviewButtons = flatten(uglyviewButtons);
    let flattenpublishButtons = flatten(uglypublishButtons);
    let flattenunpublishButtons = flatten(uglyunpublishButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }
    if (flattenviewButtons) {
      for (let i = 0; i < flattenviewButtons.length; i++) {
        flattenviewButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }

    if (flattenpublishButtons) {
      for (let i = 0; i < flattenpublishButtons.length; i++) {
        flattenpublishButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattenunpublishButtons) {
      for (let i = 0; i < flattenunpublishButtons.length; i++) {
        flattenunpublishButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
    if (value === "View") {
      this.openViewSubject();
    }
    if (value === "Publish") {
      this.openPublish();
    }
    if (value === "Unpublish") {
      this.openUnpublish();
    }
  }
  openPublish() {
    this.dialogRef = this.dialog.open(PublishSubjectComponent, {
      width: "50%",
      height: "calc(82vh)"
    });
    this.dialogRef.componentInstance.showPublish = true;
    this.dialogRef.disableClose = true;
  }
  openUnpublish() {
    this.dialogRef = this.dialog.open(PublishSubjectComponent, {
      width: "50%",
      height: "calc(82vh)"
    });
    this.dialogRef.componentInstance.showUnpublish = true;
    this.dialogRef.disableClose = true;
  }
  openNewSubject() {
    this.routes.navigateByUrl("/manage-subjects/create-new");
  }

  openViewSubject() {
    this.routes.navigateByUrl("/manage-subjects/view-subject");
  }
}
