import { Component, ViewChild, ElementRef } from "@angular/core";
import { Router } from "@angular/router";
import { jqxCalendarComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxcalendar";

@Component({
  selector: "lesson-schedule",
  templateUrl: "./lesson-schedule.component.html",
  styleUrls: ["../lesson-management.component.scss"]
})
export class LessonScheduleComponent {
  showContent: boolean ;
  @ViewChild("myCalendar")
  myCalendar: jqxCalendarComponent;
  startDateSelection: any;
  endDateSelection: any;

  constructor(private routes: Router) {}

  myCalendarOnChange(event: any): void {
    let selection = event.args.range;
    this.startDateSelection = selection.from.toLocaleDateString();
    this.endDateSelection = selection.to.toLocaleDateString();
    if (selection) {
      this.showContent = true;
    }
  }

  openNewLessonSchedule(){
      this.routes.navigateByUrl('lesson/lesson-schedule/create-new')
  }
}
