import { ClassPerformanceDetailsComponent } from "./performance-detailed-view/class-performance-summary/class-performance-details/class-performance-details.component";
import { ClassPerformanceUpdateComponent } from "./performance-detailed-view/class-performance-summary/class-performance-update/class-performance-update.component";
import { ClassroomPerformanceDetailedViewComponent } from "./performance-detailed-view/performance-detailed-view.component";
import { LearningPathDetailsComponent } from "./performance-detailed-view/publish-learning-paths/learning-paths-details/learning-path-details.component";
import { LearningPathPublishComponent } from "./performance-detailed-view/publish-learning-paths/learning-paths-publish/learning-path-publish.component";
import { NgModule } from "@angular/core";
import { PrerequisiteDetailsComponent } from "./performance-detailed-view/pre-requisite-standards-summary/pre-requisite-details/pre-requisite-details.component";
import { PrerequisiteUpdateComponent } from "./performance-detailed-view/pre-requisite-standards-summary/pre-requisite-update/pre-requisite-update.component";
import { ProjectSummaryDetailsComponent } from "./performance-detailed-view/projects-summary/projects-summary-details/projects-summary-details.component";
import { ProjectSummaryPublishComponent } from "./performance-detailed-view/projects-summary/project-summary-publish/project-summary-publish.component";
import { ProjectSummaryUpdateComponent } from "./performance-detailed-view/projects-summary/projects-summary-update/projects-summary-update.component";
import { RouterModule, Routes } from "@angular/router";
import { NewLearningPathComponent } from "./learning-path/new-learning-path/new-learning-path.component";
import { LearningPathsComponent } from "./learning-path/learning-path-list/learning-path-list.component";
import { ProjectSummaryViewDetailsComponent } from "./performance-detailed-view/projects-summary/projects-summary-view-details/projects-summary-view-details.component";
import { LearningPathsCurriculumComponent } from "./learning-path/learning-path-list-curriculum/learning-path-list-curriculum.component";
import { AssessmentsComponent } from "./assessments/assessments.component";
import { AssessmentsViewComponent } from "./assessments/assessments-view/assessments-view.component";
import { LearningRecommendationsComponent } from "./learning-recommendations/learning-recommendations.component";
import { NewLearningRecommendationsComponent } from "./learning-recommendations/new-learning-recommendations/new-learning-recommendations.component";

const routes: Routes = [
  {
    path: "class-performance/dashboard/detailed-view",
    component: ClassroomPerformanceDetailedViewComponent,
    data: { title: "Performance Detailed View" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard/details",
    component: ClassPerformanceDetailsComponent,
    data: { title: "Class Performance Details" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard/update",
    component: ClassPerformanceUpdateComponent,
    data: { title: "Class Performance Update" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard/detailed-view/prerequisite-details",
    component: PrerequisiteDetailsComponent,
    data: { title: "Pre-requisite Performance Details" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard/detailed-view/prerequisite-update",
    component: PrerequisiteUpdateComponent,
    data: { title: "Pre-requisite Performance Update" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard/detailed-view/project-view-details",
    component: ProjectSummaryViewDetailsComponent,
    data: { title: "Project Summary Details" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard/detailed-view/project-details",
    component: ProjectSummaryDetailsComponent,
    data: { title: "Project Summary Details" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard/detailed-view/project-update",
    component: ProjectSummaryUpdateComponent,
    data: { title: "Project Summary Update" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard/detailed-view/project-publish",
    component: ProjectSummaryPublishComponent,
    data: { title: "Project Summary Publish" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard/detailed-view/learning-path-publish",
    component: LearningPathPublishComponent,
    data: { title: "Learning Path Publish" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard/detailed-view/learning-path-details",
    component: LearningPathDetailsComponent,
    data: { title: "Learning Path Details" },
    pathMatch: "full"
  },
  {
    path: "class-performance/learning-paths/prerequisite-standard",
    component: LearningPathsComponent,
    data: { title: "Learning Paths" },
    pathMatch: "full"
  },
  {
    path: "class-performance/learning-paths/curriculum-lesson",
    component: LearningPathsCurriculumComponent,
    data: { title: "Learning Paths" },
    pathMatch: "full"
  },
  {
    path: "class-performance/learning-paths/new-learning-path",
    component: NewLearningPathComponent,
    data: { title: "New Learning Path" },
    pathMatch: "full"
  },
  {
    path: "class-performance/assessments",
    component: AssessmentsComponent,
    data: { title: "Assessments" },
    pathMatch: "full"
  },
  {
    path: "class-performance/assessments/assessment-view",
    component: AssessmentsViewComponent,
    data: { title: "Assessments" },
    pathMatch: "full"
  },
  {
    path: "class-performance/learning-recommendations",
    component: LearningRecommendationsComponent,
    data: { title: "Learning Recommendations" },
    pathMatch: "full"
  },
  {
    path: "class-performance/learning-recommendations/create-new",
    component: NewLearningRecommendationsComponent,
    data: { title: "New Learning Recommendations" },
    pathMatch: "full"
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ClassPerformanceRoutesModule {}
