import { Component, ViewChild } from "@angular/core";
import { MatOption } from "@angular/material";
import {
  Validators,
  FormBuilder,
  FormGroup,
  FormControl
} from "@angular/forms";
import { Router } from "@angular/router";

@Component({
  templateUrl: "./new-learning-path.component.html",
  styleUrls: ["./new-learning-path.component.scss"]
})
export class NewLearningPathComponent {
  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  thirdFormGroup: FormGroup;
  fourthFormGroup: FormGroup;
  @ViewChild("allSelected")
  private allSelected: MatOption;
  studentFilters = [
    {
      key: 1,
      value: "Ajith"
    },
    {
      key: 2,
      value: "Kumar"
    },
    {
      key: 3,
      value: "Karthik"
    },
    {
      key: 4,
      value: "Mukesh"
    }
  ];

  treeSource: any[] = [
    {
      label: "Mathematics",
      items: [
        {
          label: "Number System",
          items: [
            {
              label: "Real Numbers",
              selected: true
            },
            {
              label: "Sets",
              selected: true
            }
          ]
        },
        {
          label: "Algebra",
          items: [
            {
              label: "Polynomials",
              selected: true
            },
            {
              label: "Pairs of Linear Equations in two variables",
              selected: true
            }
          ]
        }
      ]
    }
  ];

  contentSource: any[] = [
    {
      label: "3 Videos",
      items: [
        {
          label: "Introduction to Real Numbers",
          selected: true
        },
        {
          label: "Real Numbers - brief explanation",
          selected: true
        }
      ]
    },
    {
      label: "2 Document",
      items: [
        {
          label: "Real Numbers pdf",
          selected: true
        }
      ]
    },
    {
      label: "2 Activities",
      items: [
        {
          label: "Real Numbers Activity 1",
          selected: true
        },
        {
          label: "Real Numbers Activity 2",
          selected: true
        }
      ]
    },
    {
      label: "2 Other Sources",
      items: [
        {
          label: "Real Numbers Games",
          selected: true
        }
      ]
    }
  ];

  constructor(private _formBuilder: FormBuilder, private routes: Router) {}

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      // firstCtrl: ["", Validators.required]
    });
    this.secondFormGroup = this._formBuilder.group({
      //secondCtrl: ["", Validators.required]
    });
    this.thirdFormGroup = this._formBuilder.group({
      student: new FormControl(["", Validators.required])
    });
    this.fourthFormGroup = this._formBuilder.group({
      // student: new FormControl(["", Validators.required])
    });
  }

  toggleAllSelection() {
    if (this.allSelected.selected) {
      this.thirdFormGroup.controls.student.patchValue([
        ...this.studentFilters.map(item => item.key),
        0
      ]);
    } else {
      this.thirdFormGroup.controls.student.patchValue([]);
    }
  }

  onBack() {
    this.routes.navigateByUrl("/class-performance/learning-paths");
  }
}
