import { Component, ViewChild, ViewContainerRef ,ComponentFactoryResolver, Input, ComponentRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { LectureComponent } from '../lecture/lecture.component';

export interface section {
  remove(section_index: number);
}

@Component({
  selector: 'section',
  templateUrl: './section.component.html',
  styleUrls: ['./section.component.scss']
})
export class SectionComponent {
  expandStatus: boolean = true;
  isDisabled: boolean;
  showSectionBox: boolean;
  showSectionContent: boolean = true;
  sectionTitle: string = "My Section";
  sectionTitleForm: FormGroup;
  @ViewChild('lecture', { read: ViewContainerRef }) lecture: ViewContainerRef;
  private _counter ;  
  @Input() section_index: number;
  public sectionInteraction: section;

  constructor(private formBuilder: FormBuilder , private componentFactoryResolver: ComponentFactoryResolver) { }

  ngOnInit() {
    this.sectionTitleForm = this.formBuilder.group({
      sectionTitle: ['', Validators.required],
    });
    this.addLecture();
  }

  openPanelState() {
    this.isDisabled = false;
  }

  closePanelState() {
    this.isDisabled = true;
  }

  onChangeSectionTitle() {
    this.isDisabled = true;
    this.showSectionBox = true;
    this.showSectionContent = false;
  }

  saveSectionTitle() {
    this.isDisabled = true;
    this.sectionTitle = this.sectionTitleForm.value.sectionTitle;
    if (this.sectionTitle !== "") {
      this.showSectionBox = false;
      this.showSectionContent = true;
    } else {
      this.sectionTitle = "My Section";
      this.showSectionBox = false;
      this.showSectionContent = true;
    }

  }

  cancelSectionTitle() {
    this.isDisabled = true;
    this.showSectionBox = false;
    this.showSectionContent = true;
  }

  index: number = 0;
  compInteraction
  componentsReferences = [];

  addLecture() {

    let componentFactory = this.componentFactoryResolver.resolveComponentFactory(LectureComponent);
    let componentRef: ComponentRef<LectureComponent> = this.lecture.createComponent(componentFactory);
    let currentComponent = componentRef.instance;

    currentComponent.index = ++this.index;

    currentComponent.compInteraction = this;

    this.componentsReferences.push(componentRef);
  }

  remove(index: number) {

    if (this.lecture.length < 1)
      return;

    let componentRef = this.componentsReferences.filter(x => x.instance.index == index)[0];

    let sectionIndex: number = this.lecture.indexOf(componentRef)

    this.lecture.remove(sectionIndex);

    this.componentsReferences = this.componentsReferences.filter(x => x.instance.index !== index);
  }

  removeSection(section_index) {
    this.sectionInteraction.remove(section_index)
  }
}