import { Component } from "@angular/core";
import { Coordinates } from "src/app/common-component/stacked-bar-chart-level1/stacked-bar-chart-level1";
import { CourseViewDialogComponent } from "./course-view-dialog/course-view-dialog.component";
import { MatDialogRef, MatDialog } from "@angular/material";

@Component({
  templateUrl: "./student-courses.component.html",
  styleUrls: ["./student-courses.component.scss"]
})
export class StudentCourseComponent {
  courseDialogRef: MatDialogRef<CourseViewDialogComponent>;
  showScroll1: boolean;
  showScroll2: boolean;
  showScroll3: boolean;
  showScroll4: boolean;
  values1: Coordinates[] = [
    { title: "Completed", value: 70 },
    { title: "Pending", value: 30 }
  ];
  values2: Coordinates[] = [
    { title: "Completed", value: 25 },
    { title: "Pending", value: 75 }
  ];
  values3: Coordinates[] = [
    { title: "Completed", value: 30 },
    { title: "Pending", value: 70 }
  ];
  values4: Coordinates[] = [
    { title: "Completed", value: 50 },
    { title: "Pending", value: 50 }
  ];

  constructor(private dialog: MatDialog) {}

  openViewDialog() {
    this.courseDialogRef = this.dialog.open(CourseViewDialogComponent, {
      width: "50%",
      height: "calc(47vh)"
    });
    this.courseDialogRef.disableClose = true;
  }
}
