import { ActivityContentComponent } from './content-library/project-content/project-content.component';
import { AttendanceComponent } from './attendance/attendance.component';
import { ClassroomDashboardComponent } from './classroom-dashboard/classroom-dashboard.component';
import { ClassroomPerformanceComponent } from './classroom-performance/classroom-performance.component';
import { ContentLibraryComponent } from './content-library/content-library.component';
import { CoursesComponent } from './courses/courses.component';
import { CurriculumContentComponent } from './content-library/curriculum-content/curriculum-content.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { MyLearningResourcesComponent } from './my-learning-resources/my-learning-resources.component';
import { NewCollectionComponent } from './my-learning-resources/new-collection/new-collection.component';
import { NgModule } from '@angular/core';
import { OtherLearningResourceContentComponent } from './content-library/other-resources-content/other-resources-content.component';
import { ParentCommunicationComponent } from './student-profile/parent-communication/parent-communication.component';
import { PrerequisiteContentComponent } from './content-library/prerequisite-standards-content/prerequisite-standards-content.component';
import { ProfileComponent } from './student-profile/profile/profile.component';
import { ProfilePerformanceComponent } from './student-profile/performance-summary/performance-summary.component';
import { RouterModule, Routes } from '@angular/router';
import { StudentCoursesComponent } from './student-courses/student-courses.component';
import { StudentListComponent } from './attendance/student-list/student-list.component';
import { StudentProfileComponent } from './student-profile/student-profile.component';
import { ViewProfileComponent } from './student-profile/view-profile/view-profile.component';
import { LessonProgressSummaryComponent } from './lesson-management/lesson-progress/lesson-progress-summary/lesson-progress-summary.component';
import { UpdateLessonProgressSummaryComponent } from './lesson-management/lesson-progress/update-lesson-progress-summary/update-lesson-progress-summary.component';
import { LessonProgressComponent } from './lesson-management/lesson-progress/lesson-progress.component';

const routes: Routes = [
  {
    path: "",
    redirectTo: "dashboard",
    pathMatch: "full"
  },
  {
    path: "dashboard",
    component: DashboardComponent,
    data: { title: "Dashboard" },
    pathMatch: "full"
  },
  {
    path: "classroom/dashboard",
    component: ClassroomDashboardComponent,
    data: { title: "Classroom Dashboard" },
    pathMatch: "full"
  },
  {
    path: "class-performance/dashboard",
    component: ClassroomPerformanceComponent,
    data: { title: "Class Performance Dashboard" },
    pathMatch: "full"
  },
  {
    path: "classroom/attendance",
    component: AttendanceComponent,
    data: { title: "Classroom Attendance" },
    pathMatch: "full"
  },
  {
    path: "classroom/attendance/students-list",
    component: StudentListComponent,
    data: { title: "Students List" },
    pathMatch: "full"
  },
  {
    path: "classroom/student",
    component: StudentProfileComponent,
    data: { title: "Student Profile" },
    children: [
      { path: "", redirectTo: "profile", pathMatch: "full" },
      { path: "profile", component: ProfileComponent, pathMatch: "full" },
      {
        path: "performance",
        component: ProfilePerformanceComponent,
        pathMatch: "full"
      },
      {
        path: "communication",
        component: ParentCommunicationComponent,
        pathMatch: "full"
      }
    ]
  },
  {
    path: "lesson",
    component:LessonProgressComponent ,
    data: { title: "Lesson Progress Summary" },
    children: [
      { path: "", redirectTo: "lesson-progress", pathMatch: "full" },
      {
        path: "lesson-progress",
        component: LessonProgressSummaryComponent,
        pathMatch: "full"
      },
      {
        path: "update-lesson-progress",
        component: UpdateLessonProgressSummaryComponent,
        pathMatch: "full"
      }
    ]
  },
  {
    path: "classroom/student/view-profile",
    component: ViewProfileComponent,
    pathMatch: "full"
  },
  {
    path: "content-library/dashboard",
    component: ContentLibraryComponent,
    data: { title: "Content Library" },
    pathMatch: "full"
  },
  {
    path: "content-library/dashboard/curriculum-content",
    component: CurriculumContentComponent,
    data: { title: "Curriculum Content" },
    pathMatch: "full"
  },
  {
    path: "content-library/dashboard/prerequisite-standard",
    component: PrerequisiteContentComponent,
    data: { title: "Prerequisite Standard Content" },
    pathMatch: "full"
  },
  {
    path: "content-library/dashboard/project-content",
    component: ActivityContentComponent,
    data: { title: "Project Activity Content" },
    pathMatch: "full"
  },
  {
    path: "content-library/dashboard/other-learning-resources",
    component: OtherLearningResourceContentComponent,
    data: { title: "Other Learning Resources Content" },
    pathMatch: "full"
  },
  {
    path: "student-courses",
    component: StudentCoursesComponent,
    data: { title: "Student Courses" },
    pathMatch: "full"
  },
  {
    path: "my-courses",
    component: CoursesComponent,
    data: { title: "My Courses" },
    pathMatch: "full"
  },
  {
    path: "my-learning-resources",
    component: MyLearningResourcesComponent,
    data: { title: "My Learning Resources" },
    pathMatch: "full"
  },
  {
    path: "my-learning-resources/create-new",
    component: NewCollectionComponent,
    data: { title: "Create New Learning Resources" },
    pathMatch: "full"
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{ useHash: true })],
  exports: [RouterModule]
})
export class TeacherRoutesModule {}
