import { Component, ViewChild } from "@angular/core";
import { MatDialog } from "@angular/material";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";

@Component({
  templateUrl: "./courses-subcriptions-details-new.component.html",
  styleUrls: ["./courses-subcriptions-details-new.component.scss"]
})
export class CourseSubcriptionsDetailsNewComponent {
  constructor(public dialog: MatDialog, private routes: Router) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(62vh)";
  }

  data: any[] = [
    {
      title: "Introduction to robotics",
      status: "New",
      createdby: "Ganesh",
      objective: "To learn about robotics ..",
      publishedon: "31/08/18",
      concepts: "Artificial Intelligence"
    },
    {
      title: "How to design a computer",
      status: "new",
      createdby: "Rajesh",
      objective: "To learn about computer designs ..",
      publishedon: "31/08/18",
      concepts: "Computer Architecture"
    }
  ];
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "title", type: "string" },
      { name: "status", type: "string" },
      { name: "objective", type: "string" },
      { name: "concepts", type: "string" },
      { name: "publishedon", type: "date" },
      { name: "createdby", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Title",
      dataField: "title",
      align: "center",
      cellsAlign: "center",
      width: 203
    },
    {
      text: "Status",
      dataField: "status",
      align: "center",
      cellsAlign: "center",
      width: 203
    },
    {
      text: "Objective",
      dataField: "objective",
      align: "center",
      cellsAlign: "center",
      width: 203
    },
    {
      text: "Pre-requisite Concepts",
      dataField: "concepts",
      align: "center",
      cellsAlign: "center",
      width: 203
    },
    {
      text: "Created by",
      align: "center",
      cellsAlign: "center",
      dataField: "createdby",
      width: 203
    },
    {
      text: "Published on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "publishedon",
      width: 203
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      width: 203,
      columnType: "none",
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `</div><div data-row='` +
          row +
          `' class='subscribeButton' style='color:white;background-color:#d81b60;margin-left: 58px;'></div>`
        );
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglysubscribeButtons = jqwidgets.createInstance(
      ".subscribeButton",
      "jqxButton",
      {
        width: 80,
        height: 24,
        value: "Subscribe"
      }
    );

    let flattensubscribeButtons = flatten(uglysubscribeButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }

    if (flattensubscribeButtons) {
      for (let i = 0; i < flattensubscribeButtons.length; i++) {
        flattensubscribeButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {}

  onNavigate(value) {
    this.routes.navigate([value]);
  }

  openUpdateKnowledgeLevelDialog() {
    alert("updated soon");
  }

  onBack() {
    this.routes.navigateByUrl("/my-courses");
  }
}
