import { Component } from "@angular/core";
import { Router } from "@angular/router";
import {
  FormGroup,
  FormControl,
  Validators,
  FormBuilder
} from "@angular/forms";

export interface Filter {
  disabled?: boolean;
  name: string;
  value: string;
}

@Component({
  selector: "create-student",
  templateUrl: "./create-student.component.html",
  styleUrls: ["../../school-admin.component.scss"]
})
export class CreateStudentComponent {
  isLinear = false;

  firstFormGroup: FormGroup;

  secondFormGroup: FormGroup;

  thirdFormGroup: FormGroup;

  fourthFormGroup: FormGroup;

  step = 0;

  public guardains: Filter[] = [
    { name: "Father", value: "father" },
    { name: "Mother", value: "mother" },
    { name: "Guardian", value: "guardian" }
  ];

  public grades: Filter[] = [
    {
      name: "7",
      value: "7"
    },
    {
      name: "8",
      value: "8"
    },
    {
      name: "9",
      value: "9"
    },
    {
      name: "10",
      value: "10"
    }
  ];

  public sections: Filter[] = [
    {
      name: "A",
      value: "1"
    },
    {
      name: "B",
      value: "2"
    },
    {
      name: "C",
      value: "3"
    },
    {
      name: "D",
      value: "4"
    }
  ];

  showFather: boolean;

  showMother: boolean;

  showGuardian: boolean;

  showFather2: boolean;

  showMother2: boolean;

  showGuardian2: boolean;

  selectedGuardian:string;
  
  selectedGuardian2:string;

  showSelectGuardian: boolean;

  showAddGuardian: boolean;

  isdisabled: boolean;
  
  constructor(public _formBuilder: FormBuilder, public routes: Router) {}

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      //  firstName: new FormControl(["", Validators.required]),
      //  middleName: new FormControl(["", Validators.required]),
      //  lastName: new FormControl(["", Validators.required]),
    });
    this.secondFormGroup = this._formBuilder.group({});
    this.thirdFormGroup = this._formBuilder.group({
      student: new FormControl(["", Validators.required])
    });
    this.fourthFormGroup = this._formBuilder.group({});
  }

  setStep(index: number) {
    this.step = index;
  }

  nextStep() {
    this.step++;
  }

  prevStep() {
    this.step--;
  }

  onChangeGuardianType(event){
    this.showAddGuardian = true;
    this.selectedGuardian;
    if (this.selectedGuardian == "father"){
      this.showFather = true;
    }
    else {
      this.showFather = false;
    }
    if (this.selectedGuardian == "mother"){
      this.showMother = true;
    }
    else {
      this.showMother = false;
    }
    if (this.selectedGuardian == "guardian"){
      this.showGuardian = true;
    }
    else {
      this.showGuardian = false;
    }
  }

  onChangeGuardianType2(event){
    this.showAddGuardian = false;
    this.selectedGuardian2;
    if (this.selectedGuardian2 == "father"){
      this.showFather2 = true;
    }
    else {
      this.showFather2 = false;
    }
    if (this.selectedGuardian2 == "mother"){
      this.showMother2 = true;
    }
    else {
      this.showMother2 = false;
    }
    if (this.selectedGuardian2 == "guardian"){
      this.showGuardian2 = true;
    }
    else {
      this.showGuardian2 = false;
    }
  }

  addGuardian(){
    this.showSelectGuardian = true;
    this.showAddGuardian = false;
  }

  onBack() {
    this.routes.navigateByUrl("/school-user-admin/student");
  }
}
