import { Component, ViewChild, Input } from "@angular/core";
import { Router } from "@angular/router";
import { MatDialogRef } from "@angular/material";

export interface PeriodicElement {
    name: string;
    level: string;
  }
  
  const ELEMENT_DATA: PeriodicElement[] = [
    {name: 'Ajith',level:"Low Performance"},
    {name: 'Vijay',level:"Mastered"},
    {name: 'Arya',level:"Low Performance"},
    {name: 'Surya',level:"Low Performance"},
    {name: 'Ganesh',level:"Satisfactory"},
    {name: 'Kapoor',level:"Satisfactory"},
    {name: 'Arjit',level:"Satisfactory"},
  ];
  

@Component({
  templateUrl: "./learning-path-dialog.component.html",
  styleUrls: ["./learning-path-dialog.component.scss"]
})
export class LearningPathDialogComponent {
  isLinear = false;

  @Input() completed:boolean;
  @Input() pending:boolean;
  @Input() viewAssignees:boolean;
  
  
  checkBox: boolean = true;

  threeStates: boolean = true;

  displayedColumns: string[] = ['name','level'];
  displayedColumns2: string[] = ['name'];

  dataSource = ELEMENT_DATA;

  constructor(public dialogref: MatDialogRef<LearningPathDialogComponent>) {}
  closeDialog() {
    this.dialogref.close();
  }
}
