import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";

@Component({
  templateUrl: "./performance-summary-list.component.html",
  styleUrls: ["./performance-summary-list.component.scss"]
})
export class PerformanceSummaryListComponent {
  constructor(private routes: Router) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(60vh)";
  }

  data: any[] = [
    {
      prerequisite: "Number System.Real Numbers",
      curriculum: "Number System.Real Numbers",
      student: "Rajendra",
      knowledgelevel: "Above Average"
    },
    {
      prerequisite: "Number System.Playing with numbers",
      curriculum: "Number System.Real Numbers",
      student: "Rajendra",
      knowledgelevel: "Above Average"
    },
    {
      prerequisite: "Number System.Rational Number",
      curriculum: "Number System.Real Numbers",
      student: "Rajendra",
      knowledgelevel: "Above Average"
    },
    {
      prerequisite: "Number System.Square numbers",
      curriculum: "Number System.Real Numbers",
      student: "Rajendra",
      knowledgelevel: "Above Average"
    },
    {
      prerequisite: "Number System.Cube numbers",
      curriculum: "Number System.Real Numbers",
      student: "Rajendra",
      knowledgelevel: "Above Average"
    },
    {
      prerequisite: "Number System.Square roots",
      curriculum: "Number System.Real Numbers",
      student: "Rajendra",
      knowledgelevel: "Above Average"
    },
    {
      prerequisite: "Number System.Cubes",
      curriculum: "Number System.Real Numbers",
      student: "Rajendra",
      knowledgelevel: "Above Average"
    },
    {
      prerequisite: "Number System.Cube roots",
      curriculum: "Number System.Real Numbers",
      student: "Rajendra",
      knowledgelevel: "Above Average"
    },
    {
      prerequisite: "Algebra.Polynomials",
      curriculum: "Algebra.Polynomials",
      student: "Rajendra",
      knowledgelevel: "Low Performance"
    },
    {
      prerequisite: "Algebra.Powers",
      curriculum: "Algebra.Polynomials",
      student: "Rajendra",
      knowledgelevel: "Low Performance"
    },
    {
      prerequisite: "Algebra.Algebraic Expressions",
      curriculum: "Algebra.Polynomials",
      student: "Rajendra",
      knowledgelevel: "Low Performance"
    },
    {
      prerequisite: "Algebra.Simple equations",
      curriculum: "Algebra.Polynomials",
      student: "Rajendra",
      knowledgelevel: "Low Performance"
    },
    {
      prerequisite: "Algebra.Linear Equations in Two Variables",
      curriculum: "Algebra.Pair of Linear Equations in Two Variables",
      student: "Rajendra",
      knowledgelevel: "Mastered"
    }
  ];

  source: any = {
    dataType: "json",
    dataFields: [
      { name: "prerequisite", type: "string" },
      { name: "curriculum", type: "string" },
      { name: "student", type: "string" },
      { name: "knowledgelevel", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Prerequisite Lesson Name",
      dataField: "prerequisite",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Curriculum Lesson Mapping",
      dataField: "curriculum",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Student Name",
      dataField: "student",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Knowledge Level",
      dataField: "knowledgelevel",
      align: "center",
      cellsAlign: "center"
    }
  ];

  onNavigation(value) {
    if (value) {
      this.routes.navigate([value]);
    }
    return false;
  }

  onBack() {
    this.routes.navigateByUrl("/classroom/dashboard");
  }
}
