import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtree";
@Component({
  selector: "manage-learning-path",
  templateUrl: "./manage-learning-path.component.html",
  styleUrls: ["./manage-learning-path.component.scss"]
})
export class ManageLearningPathComponent {
  
  syllabusSource: any[] = [
    {
      label: "Maths",
      expanded: true,
      items: [
        {
          label: "Algebra",
          expanded: true,
          items: [
            {
              label: "Basic Fundamentals",
              selected: true
            },
            {
              label: "Introduction",
              selected: true
            }
          ]
        },
        {
          label: "Trignometry",
          expanded: true,
          items: [
            {
              label: "Basics",
              selected: true
            },
            {
              label: "Sine and Cosine degree",
              selected: true
            }
          ]
        }
      ]
    }
  ];

  showPrequisiteStandard: boolean;
  
  @ViewChild("treeReference")
  treeReference: jqxTreeComponent;

  constructor(private routes: Router) {}


  myTreePreviewSelect(event: any): void {
    let args = event.args;
    let item = this.treeReference.getItem(args.element);
    if (item.hasItems == false) {
      this.showPrequisiteStandard = true;
    } else {
      this.showPrequisiteStandard = false;
    }
  }

  myTreeOnInitialized(): void {
    this.showPrequisiteStandard = false;
    document.addEventListener("contextmenu", event => {
      event.preventDefault();
      this.treeReference.selectItem(event.target);
    });
  }

  openLearningPath(){
    this.routes.navigateByUrl("/class-performance/learning-paths/new-learning-path");
  }

  openNewSyllabus() {
    this.routes.navigateByUrl("/manage-prerequisite-standard-mapping/create-new");
  }
}
