import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";

@Component({
  selector: "student-awards-list",
  templateUrl: "./student-awards-list.component.html",
  styleUrls: ["./student-awards-list.component.scss"]
})
export class StudentAwardsListComponent {
  constructor(private routes: Router) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  data: any[] = [
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "General",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "Sports",
      award: "Best Batsman Award",
      awardedon: "30-11-2018"
    },
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    },
    {
      category: "Academic",
      award: "Achiever Award",
      awardedon: "29-11-2018"
    }
  ];

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(62vh)";
  }

  source: any = {
    dataType: "json",
    dataFields: [
      { name: "category", type: "string" },
      { name: "award", type: "string" },
      { name: "awardedon", type: "date" }
    ],
    localData: this.data
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Category",
      dataField: "category",
      align: "center",
      editable: true,
      cellsAlign: "center",
      columnType: "template",
      createEditor: (row, cellvalue, editor, cellText, width, height) => {
        // construct the editor.
        let categories = ["Academic", "Sports", "General"];
        editor.jqxDropDownList({
          autoDropDownHeight: true,
          source: categories,
          width: "100%",
          height: "100%"
        });
      },
      initEditor: (row, cellvalue, editor, celltext, width, height) => {
        // set the editor's current value. The callback is called each time the editor is displayed.
        editor.jqxDropDownList("selectItem", cellvalue);
      },
      getEditorValue: (row, cellvalue, editor) => {
        // return the editor's value.
        return editor.val();
      }
    },
    {
      text: "Award Title",
      dataField: "award",
      align: "center",
      cellsAlign: "center",
      editable: true,
      columntype: "datetimeinput",
      validation: (cell: any, value: any): any => {
        if (value == "") return true;
        let year = value.getFullYear();
        if (year >= 2017) {
          return {
            result: false,
            message: "Ship Date should be before 1/1/2017"
          };
        }
        return true;
      }
    },
    {
      text: "Awarded on",
      align: "center",
      cellsAlign: "center",
      editable: true,
      cellsFormat: "d",
      dataField: "awardedon",
      columnType:"datetimeinput"
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rowKey: number = -1;
  rowClick(event: any): void {
    this.rowKey = event.args.key;
  }

  onNavigation(value) {
    if (value) {
      this.routes.navigate([value]);
    }
    return false;
  }

  onBack() {
    this.routes.navigateByUrl("/my-profile");
  }
}
