import { Component } from "@angular/core";
import { Router } from "@angular/router";
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl
} from "@angular/forms";

export interface ContentType {
  name: string;
  value: string;
}

export interface DataSource {
  name: string;
  contentType: string;
  contentCategory: string;
}

@Component({
  selector: "edit-other-learning-resources",
  templateUrl: "./edit-other-learning-resources.component.html",
  styleUrls: ["./edit-other-learning-resources.component.scss"]
})
export class EditOtherLearningResourcesComponent {
  showFileUpload: boolean;
  showWebURL: boolean;
  resourceTypevalue: string;
  contentForm: FormGroup;
  contentType: string = "";
  contentTitle: string = "";
  contentCategory: string = "";
  selectedContentType: string;
  selectedContentCategory: string;

  contentCategorys: ContentType[] = [
    { name: "Video", value: "1" },
    { name: "Document", value: "2" }
  ];

  contentTypes: ContentType[] = [
    { name: "File Upload", value: "1" },
    { name: "Web Source", value: "2" }
  ];

  constructor(private routes: Router, private formBuilder: FormBuilder) {}

  onContentType() {
    if (this.selectedContentType === "File Upload") {
      this.showFileUpload = true;
      this.showWebURL = false;
    } else if (this.selectedContentType === "Web Source") {
      this.showFileUpload = false;
      this.showWebURL = true;
    } else {
      this.showFileUpload = false;
      this.showWebURL = false;
    }
  }

  sources: DataSource[] = [
    {
      name: "Real Numbers - Introduction",
      contentType: "Web Source",
      contentCategory: "Video"
    },
    {
      name: "Sets - Introduction",
      contentType: "File Upload",
      contentCategory: "Video"
    }
  ];

  ngOnInit() {
    this.contentForm = this.formBuilder.group({
      contentTitle: new FormControl("", Validators.required),
      contentCategory: new FormControl("", Validators.required),
      contentType: new FormControl("", Validators.required)
    });
  }

  editContent(index: any) {
    this.contentTitle = this.contentForm.value.contentTitle;
    this.selectedContentType = this.contentForm.value.contentType;
    this.selectedContentCategory = this.contentForm.value.contentCategory;
  }

  deleteContent(index: any) {
    this.sources.splice(index, 1);
  }

  saveContent() {
    this.contentTitle = this.contentForm.value.contentTitle;
    this.contentType = this.contentForm.value.contentType;
    this.contentCategory = this.contentForm.value.contentCategory;
    if (
      this.contentTitle !== "" &&
      this.contentCategory !== "" &&
      this.contentType !== "" &&
      this.contentTitle !== null &&
      this.contentCategory !== null &&
      this.contentType !== null
    ) {
      this.sources.push({
        name: this.contentTitle,
        contentType: this.contentType,
        contentCategory: this.contentCategory
      });
      this.showFileUpload = false;
      this.showWebURL = false;
      this.contentForm.reset();
    } else {
      alert("Please fill the required fields");
    }
  }
}
