import {
  Component,
  ViewChild,
  ViewContainerRef,
  ComponentFactoryResolver,
  ComponentRef
} from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { HttpClient, HttpEventType, HttpResponse } from "@angular/common/http";
import { LessonPlanSectionComponent } from "src/app/common-component/lessonplan-section/lessonplan-section.component";
import { Router } from "@angular/router";

export interface Lesson {
  value: string;
  viewValue: string;
}

export interface Clasroom {
  value: string;
  viewValue: string;
}

export interface Reference {
  name: string;
  webURL: string;
}

export interface LessonPlan {
  title: string;
  description: string;
}

export interface Lessons {
  disabled?: boolean;
  name: string;
  lessonchildren: Lesson[];
}

@Component({
  templateUrl: "./create-lesson-plan.component.html",
  styleUrls: ["./create-lesson-plan.component.scss"]
})
export class CreateLessonPlanComponent {
  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  thirdFormGroup: FormGroup;
  fourthFormGroup: FormGroup;

  public classroom: Clasroom[] = [
    { value: "0", viewValue: "6.Mathematics" },
    { value: "1", viewValue: "7.Mathematics" },
    { value: "2", viewValue: "8.Science" },
    { value: "3", viewValue: "9.English" }
  ];

  public references: Reference[] = [
    {
      name: "Basic Real Numbers",
      webURL: "http://learning-resources.com/real_numbers"
    },
    {
      name: "Advanced Set Concepts",
      webURL: "http://learning-resources.com/set_concepts"
    }
  ];

  public lessons: Lessons[] = [
    {
      name: "Unit I - Number System",
      lessonchildren: [
        { value: "realnumbers", viewValue: "Real Numbers" },
        { value: "sets", viewValue: "Sets" }
      ]
    },
    {
      name: "Unit II - Algebra",
      lessonchildren: [
        { value: "propagation", viewValue: "Propogation" },
        { value: "quadraticequations", viewValue: "Quadratic Equations" },
        { value: "horsea-5", viewValue: "Horsea" }
      ]
    }
  ];

  public lessonplans: LessonPlan[] = [
    {
      title: "Lesson Objective",
      description: "some descriptions"
    },
    {
      title: "Pre-requisite Knowledge Evaluation",
      description: "some descriptions"
    },
    {
      title: "Introduction",
      description: "some descriptions"
    },
    {
      title: "Activities",
      description: "descriptions"
    }
  ];

  showFileUpload: boolean = true;

  showLessonPlan: boolean;

  showReferences: boolean;

  percentDone: number;

  @ViewChild("lessonplansection", { read: ViewContainerRef })
  lessonplansection: ViewContainerRef;
  section_index: number = 0;
  sectionInteraction;
  componentsReferences = [];

  constructor(
    private _formBuilder: FormBuilder,
    private routes: Router,
    private http: HttpClient,
    private componentFactoryResolver: ComponentFactoryResolver
  ) {}

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({});
    this.secondFormGroup = this._formBuilder.group({});
    this.thirdFormGroup = this._formBuilder.group({});
    this.fourthFormGroup = this._formBuilder.group({});
  }

  OpenReferenceActions() {
    this.showReferences = true;
  }

  lessonPlan(event) {
    let eventValue = event.value;
    if (eventValue === 0) {
      this.showFileUpload = true;
      this.showLessonPlan = false;
    } else if (eventValue === 1) {
      this.showLessonPlan = true;
      this.showFileUpload = false;
    } else {
      this.showLessonPlan = false;
      this.showFileUpload = false;
    }
  }

  addSection() {
    let componentFactory = this.componentFactoryResolver.resolveComponentFactory(
      LessonPlanSectionComponent
    );
    let componentRef: ComponentRef<
      LessonPlanSectionComponent
    > = this.lessonplansection.createComponent(componentFactory);
    let currentComponent = componentRef.instance;

    currentComponent.section_index = ++this.section_index;

    currentComponent.sectionInteraction = this;

    this.componentsReferences.push(componentRef);
  }

  remove(section_index: number) {
    if (this.lessonplansection.length < 1) return;

    let componentRef = this.componentsReferences.filter(
      x => x.instance.section_index == section_index
    )[0];

    let section_indexs: number = this.lessonplansection.indexOf(componentRef);

    this.lessonplansection.remove(section_indexs);

    this.componentsReferences = this.componentsReferences.filter(
      x => x.instance.section_index !== section_index
    );
  }

  upload(files: File[]) {
    this.uploadAndProgress(files);
  }

  basicUpload(files: File[]) {
    var formData = new FormData();
    Array.from(files).forEach(f => formData.append("file", f));
    this.http.post("https://file.io", formData).subscribe(event => {
      console.log("done");
    });
  }

  basicUploadSingle(file: File) {
    this.http.post("https://file.io", file).subscribe(event => {
      console.log("done");
    });
  }

  uploadAndProgress(files: File[]) {
    var formData = new FormData();
    Array.from(files).forEach(f => formData.append("file", f));

    this.http
      .post("https://file.io", formData, {
        reportProgress: true,
        observe: "events"
      })
      .subscribe(event => {
        if (event.type === HttpEventType.UploadProgress) {
          this.percentDone = Math.round((100 * event.loaded) / event.total);
        } else if (event instanceof HttpResponse) {
          alert("File Uploaded SuccessFully :)");
        }
      });
  }

  uploadAndProgressSingle(file: File) {
    this.http
      .post("https://file.io", file, {
        reportProgress: true,
        observe: "events"
      })
      .subscribe(event => {
        if (event.type === HttpEventType.UploadProgress) {
          this.percentDone = Math.round((100 * event.loaded) / event.total);
        } else if (event instanceof HttpResponse) {
          alert("File Uploaded SuccessFully :)");
        }
      });
  }

  onBack() {
    this.routes.navigateByUrl("/lesson/lesson-plans");
  }
}
