import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";

@Component({
  selector: "LessonSummaryListComponent",
  templateUrl: "./lesson-summary-list.component.html",
  styleUrls: ["./lesson-summary-list.component.scss"]
})
export class LessonSummaryListComponent {
  actionValue: string;
  constructor(private routes: Router) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(60vh)";
  }

  data: any[] = [
    {
      lesson: "Number System.Real Numbers",
      status: "Completed",
      startedon: "10-10-2018",
      prerequisiteassessmenton: "12-10-2018",
      lessonassessmenton: "13-10-2018",
      completedon: "15-10-2018"
    },
    {
      lesson: "Number System.Sets",
      status: "Revision Pending",
      startedon: "10-10-2018",
      prerequisiteassessmenton: "12-10-2018",
      lessonassessmenton: "13-10-2018",
      completedon: "15-10-2018"
    },
    {
      lesson: "Algebra.Polynomials",
      status: "In Progress",
      startedon: "10-10-2018",
      prerequisiteassessmenton: "12-10-2018",
      lessonassessmenton: "13-10-2018",
      completedon: "15-10-2018"
    },
    {
      lesson: "Algebra.Pair of Linear Equations in Two Variables ",
      status: "Revision Completed",
      startedon: "10-10-2018",
      prerequisiteassessmenton: "12-10-2018",
      lessonassessmenton: "13-10-2018",
      completedon: "15-10-2018"
    }
  ];

  source: any = {
    dataType: "json",
    dataFields: [
      { name: "lesson", type: "string" },
      { name: "startedon", type: "date" },
      { name: "prerequisiteassessmenton", type: "date" },
      { name: "lessonassessmenton", type: "date" },
      { name: "completedon", type: "date" },
      { name: "status", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Lesson",
      dataField: "lesson",
      align: "center",
      cellsAlign: "center",
      width: 180
    },
    {
      text: "Started on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "startedon",
      width: 150
    },
    {
      text: "Prerequisite assessment on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "prerequisiteassessmenton",
      width: 150
    },
    {
      text: "Lesson assessment on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "lessonassessmenton",
      width: 150
    },
    {
      text: "Completed on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "completedon",
      width: 150
    },
    {
      text: "Status",
      dataField: "status",
      align: "center",
      cellsAlign: "center",
      width: 180
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      width: 300,
      columnType: "none",
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (
        row: number,
        column: any,
        value: any,
        rowData
      ): string => {
        if (rowData.status === "Completed") {
          return (
            `<div data-row='` +
            row +
            `' class='editButton' style='color:white;background-color:orange;float:left;margin-left: 50px;'></div><div data-row='` +
            row +
            `' class='editButton' style='color:white;background-color:orange;float:left;margin-left: 50px;display:none'></div>`
          );
        } else {
          return "-";
        }
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyeditButtons = jqwidgets.createInstance(".editButton", "jqxButton", {
      width: 200,
      height: 24,
      value: "Update Lesson Progress"
    });
    let flatteneditButtons = flatten(uglyeditButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }

    if (flatteneditButtons) {
      for (let i = 0; i < flatteneditButtons.length; i++) {
        flatteneditButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };
  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    debugger;
    this.actionValue = event.target.innerText;
    if (this.actionValue === "Update Lesson Progress") {
      this.routes.navigateByUrl("/lesson/update-lesson-progress");
    }
  }

  onBack() {
    this.routes.navigateByUrl("/classroom/dashboard");
  }
}
