import { Component, ViewChild } from "@angular/core";
import { MatOption, MatDialog, MatDialogRef } from "@angular/material";
import {
  Validators,
  FormBuilder,
  FormGroup,
  FormControl
} from "@angular/forms";
import { Router } from "@angular/router";
import { SourceDialogComponent } from "../source-dialog/source-dialog.component";

@Component({
  templateUrl: "./learning-path-publish.component.html",
  styleUrls: ["./learning-path-publish.component.scss"]
})
export class LearningPathPublishComponent {
  isLinear = false;

  firstFormGroup: FormGroup;

  secondFormGroup: FormGroup;

  thirdFormGroup: FormGroup;

  fourthFormGroup: FormGroup;

  step = 0;

  @ViewChild("allSelected")
  private allSelected: MatOption;

  isAssignees: boolean = false;

  checkBox: boolean = true;

  threeStates: boolean = true;

  contentSource: any[] = [
    {
      label: "2 Videos",
      items: [
        {
          label: "Introduction to Real Numbers",
          selected: true
        },
        {
          label: "Real Numbers - brief explanation",
          selected: true
        }
      ]
    },
    {
      label: "1 Document",
      items: [
        {
          label: "Real Numbers pdf",
          selected: true
        }
      ]
    },
    {
      label: "1 Activities",
      items: [
        {
          label: "Real Numbers Activity 1",
          selected: true
        },
        {
          label: "Real Numbers Activity 2",
          selected: true
        }
      ]
    },
    {
      label: "1 Other Sources",
      items: [
        {
          label: "Real Numbers Games",
          selected: true
        }
      ]
    }
  ];

  treeSource: any[] = [
    {
      label: "Low Performance (5)",
      items: [
        {
          label: "Aravind Raja"
        },
        {
          label: "Bala Abirami"
        },
        {
          label: "Chellathaai"
        },
        {
          label: "Dravid"
        }
      ]
    },
    {
      label: "Satisfactory (4)",
      items: [
        {
          label: "Rajesh Kanna"
        },
        {
          label: "Sujeetha"
        },
        {
          label: "Suganya"
        },
        {
          label: "Sugumar"
        }
      ]
    },
    {
      label: "Above Average (4)",
      items: [
        {
          label: "Ganesh NR"
        },
        {
          label: "Krishna Chaitanya"
        },
        {
          label: "Ganesh Marri"
        },
        {
          label: "Rahul Ghi"
        }
      ]
    },
    {
      label: "Mastered (2)",
      items: [
        {
          label: "Kiran"
        },
        {
          label: "Rajendra"
        }
      ]
    },
  ];

  
  viewDialogRef: MatDialogRef<SourceDialogComponent>;

  videoSource: any[] = [
    {
      label: "Basic Real Numbers Videos",
      selected: true
    },
    {
      label: "Advanced Real Numbers Videos",
      selected: true
    }
  ];

  
  documentSource: any[] = [
    {
      label: "Real Numbers pdf",
      selected: true
    }
  ];

  projectSource: any[] = [
    {
      label: "Real Numbers Activity 1",
      selected: true
    },
    {
      label: "Real Numbers Activity 2",
      selected: true
    }
  ];

  otherSource: any[] = [
    {
      label: "Real Numbers Games",
      selected: true
    }
  ];

  studentFilters = [
    {
      key: 1,
      value: "Ajith"
    },
    {
      key: 2,
      value: "Kumar"
    },
    {
      key: 3,
      value: "Karthik"
    },
    {
      key: 4,
      value: "Mukesh"
    }
  ];

  constructor(
    private _formBuilder: FormBuilder,
    private dialog: MatDialog,
    private routes: Router
  ) {}

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      // firstCtrl: ["", Validators.required]
    });
    this.secondFormGroup = this._formBuilder.group({
      //secondCtrl: ["", Validators.required]
    });
    this.thirdFormGroup = this._formBuilder.group({
      student: new FormControl(["", Validators.required])
    });
    this.fourthFormGroup = this._formBuilder.group({
      // student: new FormControl(["", Validators.required])
    });
  }

  setStep(index: number) {
    this.step = index;
  }

  nextStep() {
    this.step++;
  }

  prevStep() {
    this.step--;
  }

  toggleAllSelection() {
    if (this.allSelected.selected) {
      this.thirdFormGroup.controls.student.patchValue([
        ...this.studentFilters.map(item => item.key),
        0
      ]);
    } else {
      this.thirdFormGroup.controls.student.patchValue([]);
    }
  }

  openAssignees() {
    this.isAssignees = true;
  }

  
  openViewAssignees(){
    this.viewDialogRef = this.dialog.open(SourceDialogComponent, {
      width: "40%",
      height: "50vh"
    });
    this.viewDialogRef.componentInstance.viewAssignees = true;
    this.viewDialogRef.disableClose = false;
}

  onBack() {
    this.routes.navigateByUrl("/class-performance/dashboard/detailed-view");
  }
}
