import { RouterModule, Routes } from "@angular/router";
import { NgModule } from "@angular/core";
import { CreateLessonPlanComponent } from "./lesson-plans/create-lesson-plan/create-lesson-plan.component";
import { NewLessonScheduleComponent } from "./lesson-schedule/new-schedule/new-schedule.component";
import { LessonScheduleComponent } from "./lesson-schedule/lesson-schedule.component";
import { LessonPlansComponent } from "./lesson-plans/lesson-plans.component";

const routes: Routes = [
  {
    path: "lesson/lesson-schedule",
    component: LessonScheduleComponent,
    pathMatch: "full"
  },
  {
    path: "lesson/lesson-plans",
    component: LessonPlansComponent,
    pathMatch: "full"
  },
  {
    path: "lesson/lesson-plans/create-new",
    component: CreateLessonPlanComponent,
    data: { title: "Create Lesson Plan" },
    pathMatch: "full" 
  },
  {
    path: "lesson/lesson-schedule/create-new",
    component: NewLessonScheduleComponent,
    data: { title: "New Lesson Schedule" },
    pathMatch: "full" 
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LessonManagementRoutesModule {}
