import { Component } from '@angular/core';
import { MatDialogRef, MatDialog } from '@angular/material';
import { StudentNewsDialogComponent } from './student-news-dialog/student-news-dialog.component';
import { Router } from '@angular/router';

@Component({
  selector: 'student-news',
  templateUrl: './student-news.component.html',
  styleUrls: ['../student-dashboard.component.scss']
})
export class StudentNewsComponent {

  newsDialogRef: MatDialogRef<StudentNewsDialogComponent>;
  showScroll:boolean = false;

  constructor(public dialog: MatDialog , private routes:Router ) { }
  openDialog() {
    this.newsDialogRef = this.dialog.open(StudentNewsDialogComponent, {
      width: '50%',
      height: 'calc(35vh)'
    });
    this.newsDialogRef.disableClose = true;
  }

  openNewsList(){
    this.routes.navigateByUrl('/my-dashboard/news-list');
  }
}
