import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";

@Component({
  templateUrl: "assessments.component.html",
  styleUrls: ["assessments.component.scss"]
})
export class AssessmentsComponent {
  constructor(private routes: Router) {}

  data: any[] = [
    {
      assessmentdate: "12/11/18",
      assessmenttype: "Curriculum Lesson",
      lesson: "Number System.Real Numbers"
    },
    {
      assessmentdate: "23/08/18",
      assessmenttype: "Prerequisite Lesson",
      lesson: "Number System.Rational Numbers"
    },
    {
      assessmentdate: "23/08/18",
      assessmenttype: "Curriculum Lesson",
      lesson: "Algebra.Polynomials"
    },
  ];

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(65vh)";
  }

  source: any = {
    dataType: "json",
    dataFields: [
      { name: "assessmentdate", type: "date" },
      { name: "assessmenttype", type: "string" },
      { name: "lesson", type: "string" }
    ],
    localData: this.data
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Assessment Date",
      dataField: "assessmentdate",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d"
    },
    {
      text: "Assessment Type",
      dataField: "assessmenttype",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Curriculum Lesson",
      dataField: "lesson",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      width: 150,
      columnType: "none",
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `<div data-row='` +
          row +
          `' class='viewButton' style='color:white;background-color:indigo;margin-left:42px;'></div>`
        );
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyViewButtons = jqwidgets.createInstance(".viewButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "View"
    });
    let flattenViewButtons = flatten(uglyViewButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }

    if (flattenViewButtons) {
      for (let i = 0; i < flattenViewButtons.length; i++) {
        flattenViewButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
    if (value === "Reassign") {
      alert("Reassigned Successfully");
    }
    if (value === "View") {
      this.routes.navigateByUrl(
        "/class-performance/assessments/assessment-view"
      );
    }
  }
}
