  constructor(
    private dialog: MatDialog,
    private routes: Router,
    private _formBuilder: FormBuilder
  ) {}

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      // firstCtrl: ["", Validators.required]
      grade: new FormControl(),
      subject: new FormControl(),
      curriculum: new FormControl()
    });
    this.secondFormGroup = this._formBuilder.group({
      student: new FormControl(["", Validators.required])
    });
    this.thirdFormGroup = this._formBuilder.group({});
  }

  setStep(index: number) {
    this.step = index;
  }

  nextStep() {
    this.step++;
  }

  prevStep() {
    this.step--;
  }

  myTreeOnInitialized(): void {
    this.myTree.selectItem(document.getElementById("Introduction"));
    this.myTree.expandItem(document.getElementById("Introduction"));
  }

  myTreeSelected(event) {
    let isSelected = event.isTrigger;
    //  this.prevElement = event.args.element.previousElementSibling;
    // this.childElement = event.args.element.childElementCount;

    if (this.prevElement !== null) {
      this.isRemoveItem = true;
    } else {
      this.isRemoveItem = false;
    }
    if (isSelected === true) {
      this.isDisabled = false;
    } else {
      this.isDisabled = true;
    }
  }

  actionOnItemClick(event: any): void {
    let item = event.currentTarget.innerText;
    let selectedItem = null;
    if (item !== null) {
      switch (item) {
        case "ADD ITEM":
          selectedItem = this.myTree.getSelectedItem();
          if (selectedItem != null) {
            this.isDisabled = false;
            let newItem = "Add item";
            this.addItem(newItem);
          } else {
            this.isDisabled = true;
          }
          break;
        case "ADD SUB ITEM":
          selectedItem = this.myTree.getSelectedItem();
          if (selectedItem != null) {
            this.isDisabled = false;
            let newItem = "Add Sub item";
            this.addItem(newItem);
          } else {
            this.isDisabled = true;
          }
          break;
        case "EDIT ITEM":
          selectedItem = this.myTree.getSelectedItem();
          if (selectedItem != null) {
            this.isDisabled = false;
            let newItem = selectedItem.label;
            this.upDateItem(newItem);
            this.myTree.render();
          } else {
            this.isDisabled = true;
          }
          break;
        case "REMOVE ITEM":
          selectedItem = this.myTree.getSelectedItem();
          if (selectedItem != null && this.prevElement !== null) {
            this.isRemoveItem = false;
            this.myTree.removeItem(selectedItem.element);
          } else {
            this.isRemoveItem = true;
          }
          break;
      }
    }
  }

  addItem(newItem: string) {
    this.showEdit = false;
    this.showAdd = true;
    this.addedItem = newItem;
  }

  upDateItem(newItem: string) {
    this.showEdit = true;
    this.showAdd = false;
    this.updatedItem = newItem;
  }

  closeItem() {
    this.showAdd = false;
    this.showEdit = false;
  }

  saveItem(event: any, newItem: string) {
    if (this.addedItem === "Add item") {
      let selectedItem = this.myTree.getSelectedItem();
      if (selectedItem != null) {
        this.addedItem = newItem;
        this.myTree.addAfter({ label: this.addedItem }, selectedItem.element);
      }
    } else {
      let selectedItem = this.myTree.getSelectedItem();
      if (selectedItem != null) {
        this.addedItem = newItem;
        this.myTree.addTo({ label: this.addedItem }, selectedItem.element);
      }
    }
    this.showAdd = false;
  }

  updateItem(event: any, newItem: string) {
    let selectedItem = this.myTree.getSelectedItem();
    if (selectedItem != null) {
      this.updatedItem = newItem;
      this.myTree.updateItem({ label: this.updatedItem }, selectedItem.element);
    }
    this.showEdit = false;
  }

  onBack() {
    this.routes.navigateByUrl("/manage-syllabus");
  }
