import { Component } from "@angular/core";
import { Router } from "@angular/router";

@Component({
  templateUrl: "courses.component.html",
  styleUrls: ["courses.component.scss"]
})
export class CoursesComponent {
  constructor(private routes: Router) {}

  openAssignCourse() {
    this.routes.navigateByUrl("/my-courses/assign-new-course");
  }

  openStudentCourseDetails() {
    this.routes.navigateByUrl("/my-courses/courses-new");
  }

  openCoursesDetailsDrafts() {
    this.routes.navigateByUrl("/my-courses/courses-details-drafts");
  }

  openCoursesDetailsApproved() {
    this.routes.navigateByUrl("/my-courses/courses-details-approved");
  }

  openCoursesDetailsPublished() {
    this.routes.navigateByUrl("/my-courses/courses-details-published");
  }

  openCoursesDetailsPendingApproval() {
    this.routes.navigateByUrl("/my-courses/courses-details-pending-approval");
  }

  openCoursesDetailsNew(){
    this.routes.navigateByUrl("/my-courses/courses-subcriptions-details-new")
  }

  openCoursesDetailsCompleted(){
    this.routes.navigateByUrl("/my-courses/courses-subcriptions-details-completed")
  }

  openCoursesDetailsPending(){
    this.routes.navigateByUrl("/my-courses/courses-subcriptions-details-pending")
  }

  openCoursesDetailsInProgress(){
    this.routes.navigateByUrl("/my-courses/courses-subcriptions-details-inprogress")
  }

}
