import { Component } from "@angular/core";
import { Router } from "@angular/router";
import { FormControl } from "@angular/forms";

export interface Lesson {
  value: string;
  viewValue: string;
}

export interface Lessons {
  disabled?: boolean;
  name: string;
  lessonchildren: Lesson[];
}

@Component({
  selector: "edit-schedule",
  templateUrl: "./new-schedule.component.html",
  styleUrls: ["../../lesson-management.component.scss"]
})
export class NewLessonScheduleComponent {

  public lessons: Lessons[] = [
    {
      name: "Unit I - Number System",
      lessonchildren: [
        { value: "realnumbers", viewValue: "Real Numbers" },
        { value: "sets", viewValue: "Sets" }
      ]
    },
    {
      name: "Unit II - Algebra",
      lessonchildren: [
        { value: "propagation", viewValue: "Propogation" },
        { value: "quadraticequations", viewValue: "Quadratic Equations" },
        { value: "horsea-5", viewValue: "Horsea" }
      ]
    }
  ];

  hideDaysPlan:boolean = true;
  showDaysPlan:boolean;


  constructor(public routes: Router) {}

  showEditPlan(){
    this.hideDaysPlan = false;
    this.showDaysPlan = true;
  }
}
