import { Component } from "@angular/core";
import { MatDialog, MatDialogRef } from "@angular/material";
import { AbsenceDialogComponent } from "./absence-dialog/absence-dialog.component";
import { StudentListComponent } from "./student-list/student-list.component";
import { Router } from "@angular/router";

@Component({
  templateUrl: "attendance.component.html",
  styleUrls: ["attendance.component.scss"]
})
export class AttendanceComponent {
  showDeselect:boolean;
  
  viewDialogRef: MatDialogRef<AbsenceDialogComponent>;
  listDialogRef: MatDialogRef<StudentListComponent>;
  constructor(private dialog: MatDialog , private routes:Router) {
  
  }

  openViewAbsentees() {
    this.viewDialogRef = this.dialog.open(AbsenceDialogComponent, {
      width: "55%",
      height: "cal(47vh)"
    });
    this.viewDialogRef.disableClose = false;
  }

  openStudentsList() {
   this.routes.navigateByUrl('classroom/attendance/students-list');
  }
}
