import { Component } from "@angular/core";
import { FormControl } from "@angular/forms";
import { Router } from "@angular/router";

export interface Field {
  value: string;
  viewValue: string;
}
@Component({
  selector: "view-curriculum",
  templateUrl: "./view-curriculum.component.html",
  styleUrls: ["./view-curriculum.component.scss"]
})
export class ViewCurriculumComponent {
  students = new FormControl();
  selected: boolean;
  states: Field[] = [
    { value: "tamilnadu", viewValue: "Tamilnadu" },
    { value: "telangana", viewValue: "Telangana" },
    { value: "andrapradesh", viewValue: "Andra Pradesh" },
    { value: "kerela", viewValue: "Kerela" }
  ];

  countries: Field[] = [
    { value: "india", viewValue: "India" },
    { value: "malaysia", viewValue: "Malaysia" },
    { value: "usa", viewValue: "United states of america" },
    { value: "africa", viewValue: "South Africa" }
  ];

  grades1: Field[] = [
    { value: "1", viewValue: "1" },
    { value: "2", viewValue: "2" },
    { value: "3", viewValue: "3" },
    { value: "4", viewValue: "4" },
    { value: "5", viewValue: "5" },
    { value: "6", viewValue: "6" }
  ];

  grades2: Field[] = [
    { value: "6", viewValue: "6" },
    { value: "7", viewValue: "7" },
    { value: "8", viewValue: "8" },
    { value: "9", viewValue: "9" },
    { value: "10", viewValue: "10" }
  ];

  constructor(private routes: Router) {}

  openViewAffilications() {
    this.routes.navigateByUrl(
      "manage-curriculums/view-curriculum/view-affiliations"
    );
  }
  Edit() {
    this.routes.navigateByUrl("/manage-curriculums/create-new");
  }
  onBack() {
    this.routes.navigateByUrl("/manage-curriculums");
  }
}
