import { Component } from "@angular/core";
import { FormGroup,  FormArray } from "@angular/forms";

export interface myinterface {
    remove(index: number);
}

@Component({
  selector: "dynamic-expansion-panel",
  templateUrl: "./dynamic-expansion-panel.component.html",
  styleUrls: ["./dynamic-expansion-panel.component.scss"]
})
export class DynamicExpansionPanelComponent {
  _ref: any;
  showForm: boolean;
  showFileUpload: boolean;
  panelOpenState = false;
  showWebURL: boolean;
  resourceTypevalue: string;

  orderForm: FormGroup;
  items: FormArray;

  public index: number;
  public selfRef: DynamicExpansionPanelComponent;
  public compInteraction: myinterface;

  openPanel(){
      this.panelOpenState = true;
  }

  removeMe(index) {
    this.compInteraction.remove(index)
  }


  onResourceType(event: any) {
    this.resourceTypevalue = event.target.value;
    if (this.resourceTypevalue === "1") {
      this.showFileUpload = true;
      this.showWebURL = false;
    } else {
      this.showFileUpload = false;
      this.showWebURL = true;
    }
  }
}
