import { Component, ViewChild } from "@angular/core";
import { MatDialog, MatDialogRef } from "@angular/material";
import { Router } from "@angular/router";
import { FilterComponent } from "../../../common-component/filter/filter.component";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";

@Component({
  selector: "lesson-plans",
  templateUrl: "./lesson-plans.component.html",
  styleUrls: ["../lesson-management.component.scss"]
})
export class LessonPlansComponent {
  showScroll: boolean;
  showScrolls: boolean;
  lessonplanFilterDialogRef: MatDialogRef<FilterComponent>;

  constructor(public dialog: MatDialog, private routes: Router) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(68vh)";
  }

  data: any[] = [
    {
      title: "Real numbers activity based",
      lesson: "Number System.Real Numbers",
      createdon: "01/02/18",
      updatedon: "4/2/18",
      createdby: "Rajendra",
      updatedby: "Rajendra",
      status: "Published"
      //view , activate , copy , print
    },
    {
      title: "Real numbers with samples",
      lesson: "Number System.Real Numbers",
      createdon: "05/02/18",
      updatedon: "8/2/18",
      createdby: "me",
      updatedby: "me",
      status: "Draft"
      //view , edit , delete , publish
    },
    {
      title: "Polynomials with new samples",
      lesson: "Algebra.Polynomials",
      createdon: "05/02/18",
      updatedon: "8/2/18",
      createdby: "me",
      updatedby: "me",
      status: "Published"
      //view , activate , edit , copy, delete
    }
  ];

  source: any = {
    dataType: "json",
    dataFields: [
      { name: "lesson", type: "string" },
      { name: "title", type: "string" },
      { name: "createdon", type: "date" },
      { name: "createdby", type: "string" },
      { name: "updatedon", type: "date" },
      { name: "updatedby", type: "string" },
      { name: "status", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Lesson Name",
      dataField: "lesson",
      align: "center",
      cellsAlign: "center",
      width: 200
    },
    {
      text: "Lesson Plan Title",
      dataField: "title",
      align: "center",
      cellsAlign: "center",
      width: 200
    },
    {
      text: "Created on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "createdon"
    },
    {
      text: "Created by",
      dataField: "createdby",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Updated on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "updatedon"
    },
    {
      text: "Updated by",
      dataField: "updatedby",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Status",
      dataField: "status",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      columnType: "none",
      width: 260,
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (
        row: number,
        column: any,
        value: any,
        rowData
      ): string => {
        if (
          rowData.status === "Published" &&
          rowData.createdby === "Rajendra"
        ) {
          return (
            `<div data-row='` +
            row +
            `' class='viewButton' style='color:white;background-color:green;float:left;margin-left: 38px;'></div><div data-row='` +
            row +
            `' class='copyButton' style='color:white;background-color:#0d47a1;float:left;'></div><div data-row='` +
            row +
            `' class='printButton' style='color:white;background-color:indigo;float:left;'></div>`
          );

          //"View Activate Copy Print";
        } else if (
          rowData.status === "Published" &&
          rowData.createdby === "me"
        ) {
          return (
            `<div data-row='` +
            row +
            `' class='viewButton' style='color:white;background-color:green;float:left;margin-left: 15px;'></div><div data-row='` +
            row +
            `' class='editButton' style='color:white;background-color:indigo;float:left;'></div><div data-row='` +
            row +
            `' class='copyButton' style='color:white;background-color:#0d47a1;float:left;'></div><div data-row='` +
            row +
            `' class='deleteButton' style='color:white;background-color:red;float:left;'></div>`
          );
          // "View Activate Edit Copy Delete";
        } else if (rowData.status === "Draft" && rowData.createdby === "me") {
          return (
            `<div data-row='` +
            row +
            `' class='viewButton' style='color:white;background-color:green;float:left;margin-left: 10px;'></div><div data-row='` +
            row +
            `' class='editButton' style='color:white;background-color:#0d47a1;float:left;'></div><div data-row='` +
            row +
            `' class='deleteButton' style='color:white;background-color:red;float:left;'></div><div data-row='` +
            row +
            `' class='publishButton' style='color:white;background-color:orange;float:left;'></div>`
          );
          // "View Edit Delete Publish";
        } else {
          return "-";
        }
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyviewButtons = jqwidgets.createInstance(".viewButton", "jqxButton", {
      width: 50,
      height: 24,
      value: "View"
    });
    let uglycopyButtons = jqwidgets.createInstance(".copyButton", "jqxButton", {
      width: 50,
      height: 24,
      value: "Copy"
    });
    let uglyprintButtons = jqwidgets.createInstance(
      ".printButton",
      "jqxButton",
      {
        width: 50,
        height: 24,
        value: "Print"
      }
    );
    let uglyeditButtons = jqwidgets.createInstance(".editButton", "jqxButton", {
      width: 50,
      height: 24,
      value: "Edit"
    });
    let uglydeleteButtons = jqwidgets.createInstance(
      ".deleteButton",
      "jqxButton",
      {
        width: 60,
        height: 24,
        value: "Delete"
      }
    );
    let uglypublishButtons = jqwidgets.createInstance(
      ".publishButton",
      "jqxButton",
      {
        width: 60,
        height: 24,
        value: "Publish"
      }
    );

    let flattenviewButtons = flatten(uglyviewButtons);
    let flattencopyButtons = flatten(uglycopyButtons);
    let flattenprintButtons = flatten(uglyprintButtons);
    let flatteneditButtons = flatten(uglyeditButtons);
    let flattendeleteButtons = flatten(uglydeleteButtons);
    let flattenpublishButtons = flatten(uglypublishButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }
    if (flattenviewButtons) {
      for (let i = 0; i < flattenviewButtons.length; i++) {
        flattenviewButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattencopyButtons) {
      for (let i = 0; i < flattencopyButtons.length; i++) {
        flattencopyButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattenprintButtons) {
      for (let i = 0; i < flattenprintButtons.length; i++) {
        flattenprintButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flatteneditButtons) {
      for (let i = 0; i < flatteneditButtons.length; i++) {
        flatteneditButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattendeleteButtons) {
      for (let i = 0; i < flattendeleteButtons.length; i++) {
        flattendeleteButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattenpublishButtons) {
      for (let i = 0; i < flattenpublishButtons.length; i++) {
        flattenpublishButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
    if (value === "View") {
      this.openView();
    }

    if (value === "Edit") {
      this.openView();
    }
  }

  openNewLessonPlan() {
    this.routes.navigateByUrl("/lesson/lesson-plans/create-new");
  }

  openView() {
    this.routes.navigateByUrl("/lesson/lesson-plans/create-new");
  }
}
