import { Component } from "@angular/core";
import { MatDialog, MatDialogRef } from "@angular/material";
import { Router } from "@angular/router";
import { FilterComponent } from "../../common-component/filter/filter.component";
import { AttendanceDialogComponent } from "./attendance-dialog/attendance-dialog.component";

@Component({
  selector: "student-profile",
  templateUrl: "./student-profile.component.html",
  styleUrls: ["./student-profile.component.scss"]
})
export class StudentProfilesComponent {
  showScroll: boolean;
  showScrolls: boolean;
  profileFilterDialogRef: MatDialogRef<FilterComponent>;
  viewDialogRef: MatDialogRef<AttendanceDialogComponent>;

  constructor(public dialog: MatDialog, private routes: Router) {}

  openAcademicList() {
    this.routes.navigateByUrl("/my-profile/academic");
  }
  openAwardsList() {
    this.routes.navigateByUrl("/my-profile/awards");
  }
  openSportsList() {
    this.routes.navigateByUrl("/my-profile/sports");
  }
  openInterestsDialog() {
    this.routes.navigateByUrl("/my-profile/interests");
  }
  openAttendanceDetails(){
      this.viewDialogRef = this.dialog.open(AttendanceDialogComponent, {
        width: "65%",
        height: "cal(70vh)"
      });
      this.viewDialogRef.disableClose = false;
  }
}
