import { Component, ViewChild } from "@angular/core";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";
import { Router } from "@angular/router";

export interface Field {
  value: string;
  viewValue: string;
}
@Component({
  selector: "affiliations-view",
  templateUrl: "./affiliations-view.component.html",
  styleUrls: ["./affiliations-view.component.scss"]
})
export class AffilicationsViewComponent {
  constructor(private routes: Router) {}

  @ViewChild("myTreeGrid")
  myTreeGrid: jqxTreeGridComponent;
  getWidth(): any {
    return "100%";
  }
  getHeight() {
    return "calc(69vh)";
  }
  data: any[] = [
    {
      CurriculumID: 1,
      CurriculumName: "CBSE 10",
      SchoolName: "Chaitanya School",
      Country: "India",
      State: "Telangana",
      City: "Gachibowli",
      SchoolOrganization: "-"
    },
    {
      CurriculumID: 2,
      CurriculumName: "CBSE 10",
      SchoolName: "Chaitanya School",
      Country: "India",
      State: "Telangana",
      City: "Lingampally",
      SchoolOrganization: "-"
    },
    {
      CurriculumID: 3,
      CurriculumName: "Stateboard 04",
      SchoolName: "Queen Mira International School",
      Country: "India",
      State: "Tamilnadu",
      City: "Madurai",
      SchoolOrganization: "-"
    },
    {
      CurriculumID: 4,
      CurriculumName: "Stateboard 04",
      SchoolName: "Velammal International School",
      Country: "India",
      State: "Tamilnadu",
      City: "Madurai",
      SchoolOrganization: "-"
    }
  ];
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "CurriculumID", type: "number" },
      { name: "CurriculumName", type: "string" },
      { name: "SchoolName", type: "string" },
      { name: "Country", type: "string" },
      { name: "State", type: "string" },
      { name: "City", type: "string" },
      { name: "SchoolOrganization", type: "string" }
    ],
    id: "CurriculumID",
    localData: this.data
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns = [
    { text: "School Name", dataField: "SchoolName", width: 200 },
    { text: "Country", dataField: "Country", width: 200 },
    { text: "State", dataField: "State", width: 200 },
    { text: "Town / City", dataField: "City", width: 200 },
    { text: "School Organization", dataField: "SchoolOrganization", width: 165 }
  ];
  ready = (): void => {
    this.myTreeGrid.expandRow(2);
  };

  onBack() {
    this.routes.navigateByUrl("/manage-curriculums/view-curriculum");
  }
}
