import { Component, ViewChild } from "@angular/core";
import { MatDialog } from "@angular/material";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";

@Component({
  templateUrl: "./courses-subcriptions-details-pending.component.html",
  styleUrls: ["./courses-subcriptions-details-pending.component.scss"]
})
export class CourseSubcriptionsDetailsPendingComponent {
  constructor(public dialog: MatDialog, private routes: Router) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(62vh)";
  }

  data: any[] = [
    {
      title: "What is rainwater harvesting ?",status:"Pending",
      publishedby: "Ganesh",
      objective: "To study about rainwater harvesting",
      subscribedon: "31/08/18",
      concepts: "Probability,Trignometry"
    },
    {
      title: "Democracy rules in india",status:"Pending",
      publishedby: "Ganesh",
      objective: "To learn about democracy rules ..",
      subscribedon: "31/08/18",
      concepts: "Probability,Trignometry",
      course: "Course 1"
    },
   
  ];
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "title", type: "string" },
      { name: "status", type: "string" },
      { name: "objective", type: "string" },
      { name: "concepts", type: "string" },
      { name: "subscribedon", type: "date" },
      { name: "publishedby", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Title",
      dataField: "title",
      align: "center",
      cellsAlign: "center",
      width: 203
    },
    {
      text: "Status",
      dataField: "status",
      align: "center",
      cellsAlign: "center",
      width: 203
    },
    {
      text: "Objective",
      dataField: "objective",
      align: "center",
      cellsAlign: "center",
      width: 203
    },
    {
      text: "Pre-requisite Concepts",
      dataField: "concepts",
      align: "center",
      cellsAlign: "center",
      width: 203
    },
    {
      text: "Published by",
      align: "center",
      cellsAlign: "center",
      dataField: "publishedby",
      width: 203
    },
    {
      text: "Subscribed on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "subscribedon",
      width: 203
    },
    {
      text: "Status",
      cellsAlign: "center",
      align: "center",
      width: 203,
      columnType: "none",
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `<div data-row='` +
          row +
          `' class='unsubcribeButton' style='color:white;background-color:red;margin-left: 58px;
          margin-top: 2px;'></div><div data-row='` +
          row +
          `' class='startButton' style='color:white;background-color:skyblue;margin-left: 72px;
          margin-top: 2px;'></div>`
        );
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyunsubcribeButtons = jqwidgets.createInstance(
      ".unsubcribeButton",
      "jqxButton",
      {
        width: 90,
        height: 24,
        value: "Unsubscribe"
      }
    );
    let uglystartButtons = jqwidgets.createInstance(
      ".startButton",
      "jqxButton",
      {
        width: 60,
        height: 24,
        value: "Start"
      }
    );
    let flattenstartButtons = flatten(uglystartButtons);
    let flattenunsubcribeButtons = flatten(uglyunsubcribeButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }
    if (flattenstartButtons) {
      for (let i = 0; i < flattenstartButtons.length; i++) {
        flattenstartButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattenunsubcribeButtons) {
      for (let i = 0; i < flattenunsubcribeButtons.length; i++) {
        flattenunsubcribeButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
  }

  onNavigate(value) {
    this.routes.navigate([value]);
  }

  onBack() {
    this.routes.navigateByUrl("/my-courses");
  }
}
