import { Component, Input, ViewChild, ElementRef } from "@angular/core";
import { MatDialogRef, MatDialog } from "@angular/material";
import { AssessmentsDialogComponent } from "../../teacher/classroom-performance/assessments/assessments-dialog/assessments-dialog.component";

export class Coordinates {
  public title: string = "";
  public value: number = 0;
}

@Component({
  selector: "stacked-bar-chart-level2",
  templateUrl: "./stacked-bar-chart-level2.html",
  styleUrls: ["./stacked-bar-chart-level2.scss"]
})
export class StackedBarChartLevel2Component {
  @ViewChild("progresscontainer")
  progresscontainer: ElementRef;

  @Input()
  values: Coordinates[] = [];

  assessmentDialog: MatDialogRef<AssessmentsDialogComponent>;

  constructor(private dialog: MatDialog) {}
  ngOnInit() {
    const total = this.values.reduce((acc, value) => {
      return acc + value.value;
    }, 0);

    var values = this.values.map((val: Coordinates) => {
      return (val.value / total) * 100;
    });

    var spans = this.progresscontainer.nativeElement.querySelectorAll("span");

    for (var i = 0; i < values.length; i++) {
      spans[i].style.width = values[i] + "%";
    }
  }
  openAssessmentsCorrect() {
    this.assessmentDialog = this.dialog.open(AssessmentsDialogComponent, {
      width: "50%",
      height: "60vh"
    });
    this.assessmentDialog.componentInstance.showCorrect = true;
    this.assessmentDialog.disableClose = true;
  }
  openAssessmentsWrong() {
    this.assessmentDialog = this.dialog.open(AssessmentsDialogComponent, {
      width: "50%",
      height: "60vh"
    });
    this.assessmentDialog.componentInstance.showWrong = true;
    this.assessmentDialog.disableClose = true;
  }
}
