import { AcademicListComponent } from './profile/academic-list/academic-list.component';
import { AssessmentSummaryListComponent } from './performance-summary/assessment-summary/assessment-summary-list/assessment-summary-list.component';
import { AssessmentSummaryUpdateComponent } from './performance-summary/assessment-summary/assessment-summary-update/assessment-summary-update.component';
import { AwardsListComponent } from './profile/awards-list/awards-list.component';
import { NewLearningPathComponent } from './performance-summary/learning-path/new-learning-path/new-learning-path.component';
import { NgModule } from '@angular/core';
import { ProjectListComponent } from './performance-summary/project/project-list/project-list.component';
import { ProjectListUpdateComponent } from './performance-summary/project/project-list-update/project-list-update.component';
import { RouterModule, Routes } from '@angular/router';
import { SportsListComponent } from './profile/sports-list/sports-list.component';
import { AssessmentSummaryListCurriculumComponent } from './performance-summary/assessment-summary/assessment-summary-list-curriculum/assessment-summary-list-curriculum.component';
import { AssessmentSummaryUpdateCurriculumComponent } from './performance-summary/assessment-summary/assessment-summary-update-curriculum/assessment-summary-update-curriculum.component';
import { AssessmentSummaryListAdhocComponent } from './performance-summary/assessment-summary/assessment-summary-list-adhoc/assessment-summary-list-adhoc.component';
import { LearningPathsListCurriculumComponent } from '../classroom-dashboard/learning-path/learning-paths-list-curriculum/learning-paths-list-curriculum.component';
import { LearningPathsListComponent } from '../classroom-dashboard/learning-path/learning-paths-list/learning-paths-list.component';
import { StudentCommunicationNewComponent } from './parent-communication/student-communication-new/student-communication-new.component';
import { InterestsComponent } from './profile/interests/interests.component';

const routes: Routes = [
  {
    path: "classroom/student/profile/academic",
    component: AcademicListComponent,
    data: { title: "Academic List" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/profile/sports",
    component: SportsListComponent,
    data: { title: "Sports List" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/profile/awards",
    component: AwardsListComponent,
    data: { title: "Awards List" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/profile/new-interest",
    component: InterestsComponent,
    data: { title: "Interests" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/performance/assessment-summary-list/prerequisite-lesson-summary",
    component: AssessmentSummaryListComponent,
    data: { title: "Assessment Summary List" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/performance/assessment-summary-list/curriculum-lesson-summary",
    component: AssessmentSummaryListCurriculumComponent,
    data: { title: "Assessment Summary List" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/performance/assessment-summary-list/adhoc-lesson-summary",
    component: AssessmentSummaryListAdhocComponent,
    data: { title: "Update Assessment Summary" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/performance/assessment-summary-list/prerequisite-lesson-summary-update",
    component: AssessmentSummaryUpdateComponent,
    data: { title: "Update Assessment Summary" },
    pathMatch: "full"
  },
 
  {
    path: "classroom/student/performance/assessment-summary-list/curriculum-lesson-summary-update",
    component: AssessmentSummaryUpdateCurriculumComponent,
    data: { title: "Update Assessment Summary" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/performance/learning-path-list/prerequisite-standard",
    component:LearningPathsListComponent,
    data: { title: "Learning Path List" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/performance/learning-path-list/curriculum-lesson",
    component:LearningPathsListCurriculumComponent,
    data: { title: "Learning Path List" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/performance/learning-path-new",
    component: NewLearningPathComponent,
    data: { title: "New Learning Path" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/performance/project-list",
    component: ProjectListComponent,
    data: { title: "Project List" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/performance/project-list-update",
    component: ProjectListUpdateComponent,
    data: { title: "Project Update List" },
    pathMatch: "full"
  },
  {
    path: "classroom/student/communication/create-new",
    component: StudentCommunicationNewComponent,
    data: { title: "Student Communication New" },
    pathMatch: "full"
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StudentProfileRoutesModule {}
