import { Component, ViewChild, Input } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";
import { LearningPathDialogComponent } from "../../classroom-dashboard/learning-path/learning-path-dialog/learning-path-dialog.component";
import { MatDialogRef, MatDialog } from "@angular/material";

@Component({
  selector: "learning-recommendations",
  templateUrl: "./learning-recommendations.component.html",
  styleUrls: ["./learning-recommendations.component.scss"]
})
export class LearningRecommendationsComponent {
  viewDialogRef:MatDialogRef<LearningPathDialogComponent>;
  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(65vh)";
  }

  data: any[] = [
    {
      title: "LR1_PreReq_LinearEquationsinTwoVariables",
      prerequisite: "Algebra_LinearEquationsinTwoVariables",
      curriculum: "Algebra_LinearEquationsinTwoVariables",
      createdon: "20/08/18",
      duedate: "31/08/18",
      assignees: "40",
      completed: "30",
      pending: "10"
    },
    {
      title: "LR1_PreReq_RealNumbers",
      prerequisite: "NumberSystem_RealNumbers",
      curriculum: "NumberSystem_RealNumbers",
      createdon: "20/10/18",
      duedate: "31/10/18",
      assignees: "30",
      completed: "10",
      pending: "20"
    },
    {
      title: "LR1_PreReq_Polynomials",
      prerequisite: "Algebra_Polynomials",
      curriculum: "Algebra_Polynomials",
      createdon: "20/09/18",
      duedate: "31/09/18",
      assignees: "50",
      completed: "25",
      pending: "25"
    }
  ];

  source: any = {
    dataType: "json",
    dataFields: [
      { name: "title", type: "string" },
      { name: "prerequisite", type: "string" },
      { name: "curriculum", type: "string" },
      { name: "createdon", type: "date" },
      { name: "duedate", type: "date" },
      { name: "assignees", type: "string" },
      { name: "completed", type: "string" },
      { name: "pending", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Title",
      dataField: "title",
      align: "center",
      cellsAlign: "center",
      width: 180
    },
    {
      text: "Pre-requisite",
      dataField: "prerequisite",
      align: "center",
      cellsAlign: "center",
      width: 180
    },
    {
      text: "Curriculum",
      dataField: "curriculum",
      align: "center",
      cellsAlign: "center",
      width: 180
    },
    {
      text: "Created on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "createdon",
      width: 150
    },
    {
      text: "Due date",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "duedate",
      width: 135
    },
    {
      text: "Total number of assignees",
      dataField: "assignees",
      align: "center",
      cellsAlign: "center",
      width: 150,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `<div data-row='` +
          row +
          `' class='assigneesButton' style='color:white;background-color:purple;margin-left:42px;'></div>`
        );
      }
    },
    {
      text: "Completed",
      dataField: "completed",
      align: "center",
      cellsAlign: "center",
      width: 150,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `<div data-row='` +
          row +
          `' class='completedButton' style='color:white;background-color:green;margin-left:42px;'></div>`
        );
      }
    },
    {
      text: "Pending",
      dataField: "pending",
      align: "center",
      cellsAlign: "center",
      width: 150,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `<div data-row='` +
          row +
          `' class='pendingButton' style='color:white;background-color:red;margin-left:42px;'></div>`
        );
      }
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      width: 280,
      columnType: "none",
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `<div data-row='` +
          row +
          `' class='viewButton' style='color:white;background-color:indigo;margin-left:42px;float:left'></div>
          <div data-row='` +
          row +
          `' class='editButton' style='color:white;background-color:green;margin:0 5px;float:left'></div>  <div data-row='` +
          row +
          `' class='deleteButton' style='color:white;background-color:red;margin:0 5px;float:left'></div>`
        );
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyViewButtons = jqwidgets.createInstance(".viewButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "View"
    });
    let flattenViewButtons = flatten(uglyViewButtons);

    let uglyEditButtons = jqwidgets.createInstance(".editButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "Edit"
    });
    let flattenEditButtons = flatten(uglyEditButtons);

    let uglyDeleteButtons = jqwidgets.createInstance(".deleteButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "Delete"
    });
    let flattenDeleteButtons = flatten(uglyDeleteButtons);


    let uglyAssigneesButtons = jqwidgets.createInstance(
      ".assigneesButton",
      "jqxButton",
      {
        width: 60,
        height: 24,
        value: "40"
      }
    );
    let flattenAssigneesButtons = flatten(uglyAssigneesButtons);

    let uglyCompletedButtons = jqwidgets.createInstance(
      ".completedButton",
      "jqxButton",
      {
        width: 60,
        height: 24,
        value: "30"
      }
    );
    let flattenCompletedButtons = flatten(uglyCompletedButtons);

    let uglyPendingButtons = jqwidgets.createInstance(
      ".pendingButton",
      "jqxButton",
      {
        width: 60,
        height: 24,
        value: "10"
      }
    );
    let flattenPendingButtons = flatten(uglyPendingButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }

    if (flattenViewButtons) {
      for (let i = 0; i < flattenViewButtons.length; i++) {
        flattenViewButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }

    if (flattenAssigneesButtons) {
      for (let i = 0; i < flattenAssigneesButtons.length; i++) {
        flattenAssigneesButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }

    if (flattenCompletedButtons) {
      for (let i = 0; i < flattenCompletedButtons.length; i++) {
        flattenCompletedButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }

    if (flattenPendingButtons) {
      for (let i = 0; i < flattenPendingButtons.length; i++) {
        flattenPendingButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }

    
    if (flattenEditButtons) {
      for (let i = 0; i < flattenEditButtons.length; i++) {
        flattenEditButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }

    if (flattenDeleteButtons) {
      for (let i = 0; i < flattenDeleteButtons.length; i++) {
        flattenDeleteButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
    if (value === "View") {
      this.routes.navigateByUrl(
        "/class-performance/learning-recommendations"
      );
    }
    if (value === "40") {
      this.openViewAssignees();
    }
    if (value === "30") {
      this.openCompleted();
    }
    if (value === "10") {
      this.openPending();
    }
  }
  constructor(private routes: Router, private dialog: MatDialog) {}
  
  openViewAssignees() {
    this.viewDialogRef = this.dialog.open(LearningPathDialogComponent, {
      width: "40%",
      height: "50vh"
    });
    this.viewDialogRef.componentInstance.viewAssignees = true;
    this.viewDialogRef.disableClose = false;
  }
  openCompleted() {
    this.viewDialogRef = this.dialog.open(LearningPathDialogComponent, {
      width: "40%",
      height: "50vh"
    });
    this.viewDialogRef.componentInstance.completed = true;
    this.viewDialogRef.disableClose = false;
  }
  openPending() {
    this.viewDialogRef = this.dialog.open(LearningPathDialogComponent, {
      width: "40%",
      height: "50vh"
    });
    this.viewDialogRef.componentInstance.pending = true;
    this.viewDialogRef.disableClose = false;
  }

  onNavigate(value) {
    this.routes.navigate([value]);
  }

  openNewPath() {
    this.routes.navigateByUrl(
      "class-performance/learning-recommendations/create-new"
    );
  }
}
