import { Component } from "@angular/core";
import { FormControl } from "@angular/forms";
import { Router } from "@angular/router";
import { MatChipInputEvent } from "@angular/material";

export interface Students {
  name: string;
}

@Component({
  selector: "student-communication-new",
  templateUrl: "./student-communication-new.component.html",
  styleUrls: ["./student-communication-new.component.scss"]
})
export class StudentCommunicationNewComponent {
  students = new FormControl();
  selected: boolean;
  messageValue: string;

  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  studentsList: Students[] = [
    { name: "Rajesh" },
    { name: "Gowtham Sankar" },
    { name: "Ashiff" }
  ];

  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    if ((value || "").trim()) {
      this.studentsList.push({ name: value.trim() });
    }

    if (input) {
      input.value = "";
    }
  }

  remove(student: Students): void {
    const index = this.studentsList.indexOf(student);

    if (index >= 0) {
      this.studentsList.splice(index, 1);
    }
  }
  constructor(public routes: Router) {}

  onChangeValue(radio) {
    let temp = radio.value;
    if (temp == "1") {
      this.messageValue = "Parent of Ganesh";
    } else {
      this.messageValue = "Ganesh";
    }
  }

  onBack(){
    this.routes.navigateByUrl('/classroom/student/communication');
  }
 
}
