import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";

@Component({
  templateUrl: "./performance-summary-list-curriculum.component.html",
  styleUrls: ["./performance-summary-list-curriculum.component.scss"]
})
export class PerformanceSummaryListCurriculumComponent {
  constructor(private routes: Router) {}

  
  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(62vh)";
  }

  data: any[] = [
    {
      lesson: "Number Systems.Cube roots",
      curriculum: "Number System.Cube roots",
      student: "Rajendra",
      knowledgelevel: "Above Average"
    },
    {
      lesson: "Number Systems.Square numbers",
      curriculum: "Number System.Square numbers",
      student: "Rajendra",
      knowledgelevel: "Low Performance"
    },
    {
      lesson: "Algebra.Polynomials",
      curriculum: "Algebra.Polynomials",
      student: "Rajendra",
      knowledgelevel: "Mastered"
    }  
  ];

    
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "lesson", type: "string" },
      { name: "curriculum", type: "string" },
      { name: "student", type: "string" },
      { name: "knowledgelevel", type: "string" },
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Lesson Name",
      dataField: "lesson",
      align: "center",
      cellsAlign: "center",
    },
    {
      text: "Curriculum Lesson Mapping",
      dataField: "curriculum",
      align: "center",
      cellsAlign: "center",
     },
    {
      text: "Student Name",
      dataField: "student",
      align: "center",
      cellsAlign: "center",
    },
    {
      text: "Knowledge Level",
      dataField: "knowledgelevel",
      align: "center",
      cellsAlign: "center",
    },
  ];
 

  onNavigation(value) {
    if (value) {
      this.routes.navigate([value]);
    }
    return false;
  }

  onBack() {
    this.routes.navigateByUrl("/classroom/dashboard");
  }
}
