import { Component, ViewChild, ViewContainerRef, ComponentFactoryResolver, ComponentRef } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators
} from "@angular/forms";
import { Router } from "@angular/router";
import { SectionComponent } from "src/app/common-component/section/section.component";

@Component({
  templateUrl: "./new-course.component.html",
  styleUrls: ["./new-course.component.scss"]
})
export class NewCourseComponent {
  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  thirdFormGroup: FormGroup;
  fourthFormGroup: FormGroup;
  showSubject: boolean;
  showGrade: boolean;
  showStudentGrade: boolean;
  showParentGrade: boolean;
  subjectValue: any;
  selected: any;
  subjects1 = [
    { value: "telugu", valueFieldType: "false", viewValue: "Telugu" },
    { value: "english", valueFieldType: "false", viewValue: "English" },
    { value: "maths", valueFieldType: "false", viewValue: "Maths" },
    { value: "science", valueFieldType: "false", viewValue: "Science" },
    { value: "general", valueFieldType: "true", viewValue: "General" }
  ];
  subjects2 = [
    { value: "telugu", valueFieldType: "false", viewValue: "Telugu" },
    { value: "english", valueFieldType: "false", viewValue: "English" },
    { value: "maths", valueFieldType: "false", viewValue: "Maths" },
    { value: "science", valueFieldType: "false", viewValue: "Science" },
    { value: "general", valueFieldType: "true", viewValue: "General" }
  ];
  gradeValue: any;
  grades1 = [
    { value: "1", viewValue: "1 Standard" },
    { value: "2", viewValue: "2 Standard" },
    { value: "3", viewValue: "3 Standard" },
    { value: "4", viewValue: "4 Standard" },
    { value: "5", viewValue: "5 Standard" }
  ];
  grades2 = [
    { value: "1", viewValue: "1 Standard" },
    { value: "2", viewValue: "2 Standard" },
    { value: "3", viewValue: "3 Standard" },
    { value: "4", viewValue: "4 Standard" },
    { value: "5", viewValue: "5 Standard" }
  ];
  grades3 = [
    { value: "1", viewValue: "1 Standard" },
    { value: "2", viewValue: "2 Standard" },
    { value: "3", viewValue: "3 Standard" },
    { value: "4", viewValue: "4 Standard" },
    { value: "5", viewValue: "5 Standard" }
  ];
  checkTeacher: boolean = true;
  checkParent: boolean;
  checkStudent: boolean;

  template1Source: any[] = [
    {
      label: "Category",
      expanded: true,
      items: [
        {
          label: "Unit",
          expanded: true,
          items: [
            {
              label: "Lesson",
              selected: true
            }
          ]
        }
      ]
    }
  ];

  studentFilters = [
    {
      key: 1,
      value: "Ajith"
    },
    {
      key: 2,
      value: "Kumar"
    },
    {
      key: 3,
      value: "Karthik"
    },
    {
      key: 4,
      value: "Mukesh"
    }
  ];

  sectionSource: any[] = [
    {
      label: "Introduction",
      items: [
        {
          label: "Unit I - Number System ",
          items: [
            {
              label: "Real Numbers",
              selected: true
            },
            {
              label: "Sets",
              selected: true
            }
          ]
        }
      ]
    }
  ];

  targetSource: any[] = [
    {
      label: "Teachers",
      items: [
        {
          label: "Telugu",
          selected: true
        },
        {
          label: "English",
          selected: true
        },
        {
          label: "Maths",
          selected: true
        }
      ]
    },
    {
      label: "Parents",
      items: [
        {
          label: "6 standard",
          selected: true
        },
        {
          label: "8 standard",
          selected: true
        }
      ]
    },
    {
      label: "Students",
      items: [
        {
          label: "6 standard",
          selected: true
        },
        {
          label: "8 standard",
          selected: true
        }
      ]
    }
  ];

  templateSource: any[] = [
    {
      label: "Section"
    }
  ];

  @ViewChild('section', { read: ViewContainerRef }) section: ViewContainerRef;
  section_index: number = 0;
  sectionInteraction
  componentsReferences = [];

  constructor(
    private _formBuilder: FormBuilder,
    private routes: Router,
    private componentFactoryResolver: ComponentFactoryResolver
  ) {}

  ngOnInit() {
    this.addSection();
    this.firstFormGroup = this._formBuilder.group({
      subject: [""],
      grade: [""]
    });
    this.secondFormGroup = this._formBuilder.group({
      subject: [""],
      grade: [""]
    });
    this.thirdFormGroup = this._formBuilder.group({
      student: new FormControl(["", Validators.required])
    });
    this.fourthFormGroup = this._formBuilder.group({});
  }

  onTeacherValue(changeValue) {
    let filter = changeValue.value;

    if (filter === "subject") {
      this.showSubject = true;
      this.showGrade = false;
    } else if (filter === "grade") {
      this.showSubject = false;
      this.showGrade = true;
    } else {
      this.showGrade = false;
      this.showSubject = false;
    }
  }

  onStudentValue(changeValue) {
    let filter = changeValue.value;

    if (filter === "grade") {
      this.showStudentGrade = true;
    } else {
      this.showStudentGrade = false;
    }
  }

  onParentValue(changeValue) {
    let filter = changeValue.value;

    if (filter === "grade") {
      this.showParentGrade = true;
    } else {
      this.showParentGrade = false;
    }
  }


  addSection() {
    let componentFactory = this.componentFactoryResolver.resolveComponentFactory(SectionComponent);
    let componentRef: ComponentRef<SectionComponent> = this.section.createComponent(componentFactory);
    let currentComponent = componentRef.instance;

    currentComponent.section_index = ++this.section_index;

    currentComponent.sectionInteraction = this;

    this.componentsReferences.push(componentRef);
    
  }

  remove(section_index: number) {

    if (this.section.length < 1)
      return;

    let componentRef = this.componentsReferences.filter(x => x.instance.section_index == section_index)[0];

    let sectionsection_index: number = this.section.indexOf(componentRef)

    this.section.remove(sectionsection_index);

    this.componentsReferences = this.componentsReferences.filter(x => x.instance.section_index !== section_index);

  }

  openCoursePlayer(){
    this.routes.navigateByUrl('/courses/course-player');
  }

  onBack() {
    this.routes.navigateByUrl("/my-courses");
  }
}
