import { Component, Input } from "@angular/core";
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl
} from "@angular/forms";
import { ComponentRef } from "@angular/core/src/render3";

export interface lecture {
  remove(index: number);
}

@Component({
  selector: "activity-panel",
  templateUrl: "./activity-panel.component.html",
  styleUrls: ["./activity-panel.component.scss"]
})
export class ActivityPanelComponent {
  expandStatus: boolean = true;
  isDisabled: boolean;
  showLectureBox: boolean;
  showLectureContent: boolean = true;
  showContentActions: boolean;
  showAddContent: boolean = true;
  showVideoContent: boolean;
  showPresentationContent: boolean;
  showDocumentContent: boolean;
  showImageContent: boolean;
  showTextContent: boolean;
  showDescription: boolean = true;
  showDescriptionBox: boolean;
  lectureTitle: string = "My Title";
  content: string = "";
  lectureTitleForm: FormGroup;
  @Input() index: number;
  ref: ComponentRef<any>;
  public compInteraction: lecture;

  constructor(private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.lectureTitleForm = this.formBuilder.group({
      lectureTitle: ["", Validators.required]
    });
  }

  openPanelState() {
    this.isDisabled = false;
  }

  closePanelState() {
    this.isDisabled = true;
  }

  onChangeLectureTitle() {
    this.isDisabled = true;
    this.showAddContent = false;
    this.showLectureBox = true;
    this.showDescription = false;
    this.showLectureContent = false;
    this.isDisabled = true;
  }

  saveLectureTitle() {
    this.isDisabled = false;
    this.lectureTitle = this.lectureTitleForm.value.lectureTitle;
    if (this.lectureTitle !== "") {
      this.showLectureBox = false;
      this.showLectureContent = true;
      this.showDescription = true;
      this.showAddContent = true;
    } else {
      this.lectureTitle = "My Title";
      this.showLectureBox = false;
      this.showLectureContent = true;
      this.showDescription = true;
      this.showAddContent = false;
    }
  }

  cancelLectureTitle() {
    this.isDisabled = true;
    this.showLectureBox = false;
    this.showLectureContent = true;
    this.showDescription = true;
  }

  addContent() {
    this.showContentActions = true;
    this.isDisabled = true;
    this.showDescription = false;
    this.showDescriptionBox = false;
    this.showLectureBox = false;
    this.showLectureContent = true;
  }

  backContent() {
    this.showDescription = true;
    this.showContentActions = false;
    this.showDescriptionBox = false;
    this.showLectureBox = false;
  }

  openContent(event) {
    this.showContentActions = false;
    this.showLectureBox = false;
    this.showLectureContent = true;
    this.content = event.currentTarget.innerText;
    if (this.content === "VIDEO") {
      this.showVideoContent = true;
      this.showImageContent = false;
      this.showPresentationContent = false;
      this.showDocumentContent = false;
      this.showTextContent = false;
      this.showAddContent = false;
    } else if (this.content === "IMAGE") {
      this.showImageContent = true;
      this.showVideoContent = false;
      this.showPresentationContent = false;
      this.showDocumentContent = false;
      this.showTextContent = false;
      this.showAddContent = false;
    } else if (this.content === "PRESENTATION") {
      this.showPresentationContent = true;
      this.showVideoContent = false;
      this.showImageContent = false;
      this.showDocumentContent = false;
      this.showTextContent = false;
      this.showAddContent = false;
    } else if (this.content === "DOCUMENT") {
      this.showDocumentContent = true;
      this.showVideoContent = false;
      this.showImageContent = false;
      this.showPresentationContent = false;
      this.showAddContent = false;
      this.showTextContent = false;
    } else {
      this.showTextContent = true;
      this.showVideoContent = false;
      this.showImageContent = false;
      this.showDocumentContent = false;
      this.showAddContent = false;
      this.showPresentationContent = false;
    }
  }

  closeVideoContent() {
    this.showVideoContent = false;
    this.showDescription = true;
    this.showAddContent = true;
    this.showLectureContent = true;
  }

  closeImageContent() {
    this.showImageContent = false;
    this.showDescription = true;
    this.showAddContent = true;
    this.showLectureContent = true;
  }

  closePresentationContent() {
    this.showPresentationContent = false;
    this.showDescription = true;
    this.showAddContent = true;
    this.showLectureContent = true;
  }
  closeDocumentContent() {
    this.showDocumentContent = false;
    this.showDescription = true;
    this.showAddContent = true;
    this.showLectureContent = true;
  }
  closeTextContent() {
    this.showTextContent = false;
    this.showDescription = true;
    this.showAddContent = true;
    this.showLectureContent = true;
  }
  openDescriptionBox() {
    this.showDescriptionBox = true;
    this.showDescription = false;
    this.showLectureContent = true;
  }

  saveDescription() {
    alert("Lecture Description added");
    this.showDescriptionBox = false;
    this.showDescription = true;
    this.showLectureContent = true;
  }

  cancelDescription() {
    this.showDescriptionBox = false;
    this.showDescription = true;
    this.showLectureContent = true;
  }

  removeLecture(index) {
    this.compInteraction.remove(index);
  }
}
