import { Component, ViewChild } from "@angular/core";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";
import { jqxDropDownListComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxdropdownlist";
import { jqxListBoxComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxlistbox";
import { NewResourceDialogComponent } from "./new-resource-dialog/new-resource-dialog.component";
import { MatDialog, MatDialogRef } from "@angular/material";
import { Router } from "@angular/router";

@Component({
  templateUrl: "my-learning-resources.component.html",
  styleUrls: ["my-learning-resources.component.scss"]
})
export class MyLearningResourcesComponent {
  showAddResources: boolean;
  resourcesDialogRef: MatDialogRef<NewResourceDialogComponent>;
  @ViewChild("myTreeGrid")
  myTreeGrid: jqxTreeGridComponent;
  @ViewChild("myDropDownList")
  myDropDownList: jqxDropDownListComponent;
  @ViewChild("myListBox")
  myListBox: jqxListBoxComponent;
  handleCheckChange: boolean = true;
  getWidth(): any {
    return "100%";
  }
  getHeight() {
    return "calc(62vh)";
  }
  listSource: any[] = [{ label: "Subject Name", value: "subject" }];
  data: any[] = [
    {
      SubjectID: 1,
      subject: "Mathematics",
      ResourceName: "Probability",
      URL: "www.studymaths.com/probability",
      Addedon: "19/08/18",
      Description: "Something"
    },
    {
      SubjectID: 2,
      subject: "Science",
      ResourceName: "Measurements",
      URL: "science-measurements.com",
      Addedon: "10/08/18",
      Description: "Something"
    },
    {
      SubjectID: 3,
      subject: "Social Science",
      ResourceName: "Democracy",
      URL: "www.democracy.com/india",
      Addedon: "18/06/18",
      Description: "Something"
    },
    {
      SubjectID: 4,
      subject: "Social Science",
      ResourceName: "Independence Rules in India",
      URL: "www.independence.com/india",
      Addedon: "18/06/18",
      Description: "Something"
    }
  ];
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "SubjectID", type: "number" },
      { name: "subject", type: "string" },
      { name: "ResourceName", type: "string" },
      { name: "URL", type: "string" },
      { name: "Addedon", type: "date" },
      { name: "Description", type: "string" }
    ],
    id: "SubjectID",
    localData: this.data
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns = [
    { text: "Resource Name", width: 180, dataField: "ResourceName" },
    { text: "URL", width: 180, dataField: "URL" },
    { text: "Added on", width: 180, dataField: "Addedon", cellsFormat: "d" },
    { text: "Description", width: 180, dataField: "Description" },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      width: 180,
      columnType: "none",
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `<div data-row='` +
          row +
          `' class='editButton' style='color:white;background-color:indigo;float:left;margin:0 10px'></div><div data-row='` +
          row +
          `' class='deleteButton' style='color:white;background-color:red;float:left;margin:0 10px'></div>`
        );
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyeditButtons = jqwidgets.createInstance(".editButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "Edit"
    });
    let uglydeleteButtons = jqwidgets.createInstance(
      ".deleteButton",
      "jqxButton",
      {
        width: 60,
        height: 24,
        value: "Delete"
      }
    );

    let flatteneditButtons = flatten(uglyeditButtons);
    let flattendeleteButtons = flatten(uglydeleteButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }

    if (flatteneditButtons) {
      for (let i = 0; i < flatteneditButtons.length; i++) {
        flatteneditButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattendeleteButtons) {
      for (let i = 0; i < flattendeleteButtons.length; i++) {
        flattendeleteButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
    if ((value = "View")) {
      // this.openView()
    }

    if ((value = "Edit")) {
      //this.openView()
    }
  }

  ready = (): void => {
    this.myTreeGrid.expandRow(2);
    this.updatemyListBox("subject");
  };
  updatemyListBox(dataField: string): void {
    this.myTreeGrid.clearFilters();
    let myListBoxAdapter = new jqx.dataAdapter(this.source, {
      uniqueDataFields: [dataField],
      async: false,
      autoBind: true
    });
    let uniqueRecords = myListBoxAdapter.records;
    uniqueRecords.splice(0, 0);
    this.myListBox.setOptions({
      source: uniqueRecords,
      displayMember: dataField
    });
    this.myListBox.checkAll();
  }
  applyFilter(dataField: string): void {
    this.myTreeGrid.clearFilters();
    let filtertype = "stringfilter";
    // create a new group of filters.
    let filtergroup = new jqx.filter();
    // get listbox's checked items.
    let checkedItems = this.myListBox.getCheckedItems();
    if (checkedItems.length == 0) {
      let filter_or_operator = 1;
      let filtervalue = "Empty";
      let filtercondition = "equal";
      let filter = filtergroup.createfilter(
        filtertype,
        filtervalue,
        filtercondition
      );
      filtergroup.addfilter(filter_or_operator, filter);
    } else {
      for (let i = 0; i < checkedItems.length; i++) {
        let filter_or_operator = 1;
        // set filter's value.
        let filtervalue = checkedItems[i].label;
        // set filter's condition.
        let filtercondition = "equal";
        // create new filter.
        let filter = filtergroup.createfilter(
          filtertype,
          filtervalue,
          filtercondition
        );
        // add the filter to the filter group.
        filtergroup.addfilter(filter_or_operator, filter);
      }
    }
    // add the filters.
    this.myTreeGrid.addFilter(dataField, filtergroup);
    // apply the filters.
    this.myTreeGrid.applyFilters();
  }

  constructor(public dialog: MatDialog, public routes: Router) {}

  myListBoxOnCheckChange(event: any): void {
    if (!this.handleCheckChange) return;
    if (event.args.label != "(Select All)") {
      // update the state of the "Select All" listbox item.
      this.handleCheckChange = false;
      this.myListBox.checkIndex(0);
      let checkedItems = this.myListBox.getCheckedItems();
      let items = this.myListBox.getItems();
      if (checkedItems.length == 1) {
        this.myListBox.uncheckIndex(0);
      } else if (items.length != checkedItems.length) {
        this.myListBox.indeterminateIndex(0);
      }
      this.handleCheckChange = true;
    } else {
      // check/uncheck all items if "Select All" is clicked.
      this.handleCheckChange = false;
      if (event.args.checked) {
        this.myListBox.checkAll();
      } else {
        this.myListBox.uncheckAll();
      }
      this.handleCheckChange = true;
    }
  }
  myDropDownListOnSelect(event: any): void {
    this.updatemyListBox(event.args.item.value);
  }
  applyBtnOnClick(): void {
    let dataField = this.myDropDownList.getSelectedItem().value;
    this.applyFilter(dataField);
  }
  clearBtnOnClick(): void {
    this.myTreeGrid.clearFilters();
  }
  openResourcesDialog() {
    this.resourcesDialogRef = this.dialog.open(NewResourceDialogComponent, {
      width: "30%",
      height: "calc(63vh)"
    });
    this.resourcesDialogRef.disableClose = true;
  }

  openNewCollection() {
    this.routes.navigateByUrl("/my-learning-resources/create-new");
  }
}
