import { Component, ViewChild, ViewContainerRef ,ComponentFactoryResolver, Input, ComponentRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';

export interface section {
  remove(section_index: number);
}

@Component({
  selector: 'lessonplan-section',
  templateUrl: './lessonplan-section.component.html',
  styleUrls: ['./lessonplan-section.component.scss']
})
export class LessonPlanSectionComponent {
  expandStatus: boolean = true;
  isDisabled: boolean;
  showSectionBox: boolean;
  sectionTitle: string = "My Lesson Plan Section";
  sectionTitleForm: FormGroup;

  @Input() section_index: number;
  public sectionInteraction: section;
  

  constructor(private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.sectionTitleForm = this.formBuilder.group({
      title: new FormControl(),
    });
  }

  openPanelState() {
    this.isDisabled = false;
  }

  closePanelState() {
    this.isDisabled = true;
  }

  onChangeSectionTitle() {
    this.isDisabled = true;
    this.showSectionBox = true;
  }

  saveSectionTitle() {
    this.isDisabled = true;
    this.sectionTitle = this.sectionTitleForm.value.title;
    if (this.sectionTitle !== "" && this.sectionTitle !== null) {
      this.showSectionBox = false;
      alert("Section title changed successfully")
    } else {
      this.sectionTitle = "My Lesson Plan Section";
      this.showSectionBox = false;
    }

  }

  cancelSectionTitle() {
    this.isDisabled = true;
    this.showSectionBox = false;
  }

  addContent() {
    this.isDisabled = true;
    alert("RT EDITOR TEXT");
  }

  removeSection(section_index) {
    this.sectionInteraction.remove(section_index)
  }
}