import { Component, ViewChild, Input } from "@angular/core";
import { Router } from "@angular/router";
import { MatDialogRef } from "@angular/material";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";

@Component({
  templateUrl: "./subcribers-grid-dialog.component.html",
  styleUrls: ["./subcribers-grid-dialog.component.scss"]
})
export class SubscribersDialogComponent {
  constructor(private dialogRef: MatDialogRef<SubscribersDialogComponent>) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  data: any[] = [
    {
      name: "Rajendra",
      subscribedon: "19-02-18",
      role:"Student",
      status: "New"
    },
    {
      name: "Rajesh",
      subscribedon: "19-02-18",
      role:"Student",
      status: "Pending"
    },
    {
      name: "Sujathaa",
      subscribedon: "15-02-18",
      role:"Student",
      status: "In Progress"
    },
    {
      name: "Sabarish",
      subscribedon: "10-05-18",
      role:"Student",
      status: "Completed"
    }
  ];

  source: any = {
    dataType: "json",
    dataFields: [
      { name: "name", type: "string" },
      { name: "role", type: "string" },
      { name: "subscribedon", type: "date" },
      { name: "status", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Names",
      dataField: "name",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Role",
      dataField: "role",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Subscribed on",
      dataField: "subscribedon",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d"
    },
    {
      text: "Status",
      dataField: "status",
      align: "center",
      cellsAlign: "center"
    }
  ];
  closeDialog() {
    this.dialogRef.close();
  }
}
