import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  templateUrl: "./student-content-library.component.html",
  styleUrls: ["./student-content-library.component.scss"]
})
export class StudentContentLibraryComponent {
  showScroll1: boolean;
  showScroll2: boolean;
  showScroll3: boolean;
  showScroll4: boolean;

  constructor(private routes: Router) {}

  openCurriculumContent() {
    this.routes.navigateByUrl("/content-library/curriculum-content");
  }
  openPrerequisiteContent() {
    this.routes.navigateByUrl("/content-library/prerequisite-standard");
  }
  openProjectContent() {
    this.routes.navigateByUrl("/content-library/project-content");
  }
  openLearningResources() {
    this.routes.navigateByUrl("/content-library/other-learning-resources");
  }
}
