import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { StudentDashboardComponent } from "./student-dashboard/student-dashboard.component";
import { StudentNewsListComponent } from "./student-dashboard/student-news/student-news-list/student-news-list.component";
import { StudentCircularListComponent } from "./student-dashboard/student-circulars/student-circulars-list/student-circulars-list.component";
import { StudentProfilesComponent } from "./student-profile/student-profile.component";
import { StudentInterestsComponent } from "./student-profile/student-interests/student-interests.component";
import { StudentAcademicListComponent } from "./student-profile/student-academic-list/student-academic-list.component";
import { StudentSportsListComponent } from "./student-profile/student-sports-list/student-sports-list.component";
import { StudentAwardsListComponent } from "./student-profile/student-awards-list/student-awards-list.component";
import { StudentContentLibraryComponent } from "./student-content-library/student-content-library.component";
import { StudentCurriculumContentComponent } from "./student-content-library/curriculum-content/curriculum-content.component";
import { StudentPrerequisiteContentComponent } from "./student-content-library/prerequisite-standards-content/prerequisite-standards-content.component";
import { StudentActivityContentComponent } from "./student-content-library/project-content/project-content.component";
import { StudentOtherLearningResourceContentComponent } from "./student-content-library/other-resources-content/other-resources-content.component";
import { StudentLearningPathComponent } from "./student-learning-path/student-learning-path.component";
import { StudentCourseComponent } from "./student-courses/student-courses.component";

const routes: Routes = [
  {
    path: "my-dashboard",
    data: { title: "My Dashboard" },
    component: StudentDashboardComponent,
    pathMatch: "full"
  },
  {
    path: "my-dashboard/news-list",
    data: { title: "News List" },
    component: StudentNewsListComponent,
    pathMatch: "full"
  },
  {
    path: "my-dashboard/circular-list",
    data: { title: "Circular List" },
    component: StudentCircularListComponent,
    pathMatch: "full"
  },
  {
    path: "my-profile",
    data: { title: "My Profile" },
    component: StudentProfilesComponent,
    pathMatch: "full"
  },
  {
    path: "my-profile/interests",
    data: { title: "Student Interest" },
    component: StudentInterestsComponent,
    pathMatch: "full"
  },
  {
    path: "my-profile/academic",
    data: { title: "Student Academic" },
    component: StudentAcademicListComponent,
    pathMatch: "full"
  },
  {
    path: "my-profile/sports",
    data: { title: "Student Sports" },
    component: StudentSportsListComponent,
    pathMatch: "full"
  },
  {
    path: "my-profile/awards",
    data: { title: "Student Awards" },
    component: StudentAwardsListComponent,
    pathMatch: "full"
  },
  {
    path: "content-library",
    data: { title: "Student Content Library" },
    component: StudentContentLibraryComponent,
    pathMatch: "full"
  },
  {
    path: "content-library/curriculum-content",
    component: StudentCurriculumContentComponent,
    data: { title: "Curriculum Content" },
    pathMatch: "full"
  },
  {
    path: "content-library/prerequisite-standard",
    component: StudentPrerequisiteContentComponent,
    data: { title: "Prerequisite Standard Content" },
    pathMatch: "full"
  },
  {
    path: "content-library/project-content",
    component: StudentActivityContentComponent,
    data: { title: "Project Activity Content" },
    pathMatch: "full"
  },
  {
    path: "content-library/other-learning-resources",
    component: StudentOtherLearningResourceContentComponent,
    data: { title: "Other Learning Resources Content" },
    pathMatch: "full"
  },
  {
    path: "learning-path",
    component: StudentLearningPathComponent,
    data: { title: "Learning Path" },
    pathMatch: "full"
  },
  {
    path: "courses",
    component: StudentCourseComponent,
    data: { title: "Course" },
    pathMatch: "full"
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class StudentRoutesModule {}
