import { Component } from "@angular/core";
import { Router } from "@angular/router";
import { AssessmentsDialogComponent } from "../../teacher/classroom-performance/assessments/assessments-dialog/assessments-dialog.component";
import { MatDialogRef, MatDialog } from "@angular/material";

export interface PeriodicElement {
  questions: number;
  level: number;
}

const ELEMENT_DATA: PeriodicElement[] = [
  { questions: 10, level: 2 },
  { questions: 5, level: 1 },
  { questions: 8, level: 3 }
];

@Component({
  templateUrl: "manage-questions.component.html",
  styleUrls: ["manage-questions.component.scss"]
})
export class ManageQuestionsComponent {

  questionsDialog: MatDialogRef<AssessmentsDialogComponent>;

  constructor(private dialog: MatDialog, private routes: Router) {}

  displayedColumns: string[] = ["questions", "level"];
  dataSource = ELEMENT_DATA;

  treeSource: any[] = [
    {
      label: "Number System",
      items: [
        {
          label: "Real Numbers",
          selected: true
        },
        {
          label: "Sets",
          selected: true
        }
      ]
    },
    {
      label: "Algebra",
      items: [
        {
          label: "Polynomials",
          selected: true
        },
        {
          label: "Pairs of Linear Equations in two variables",
          selected: true
        }
      ]
    }
  ];

  openNewQuestion() {
    this.routes.navigateByUrl("/manage-questions/create-new");
  }

  openQuestions() {
    this.questionsDialog = this.dialog.open(AssessmentsDialogComponent, {
      width: "50%",
      height: "60vh"
    });
    this.questionsDialog.componentInstance.showCorrect = true;
    this.questionsDialog.disableClose = true;
  }
}
