import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { DashboardComponent } from "./teacher/dashboard/dashboard.component";
import { StudentComponent } from "./school-admin/student/student.component";
import { ManageCurriculumsComponent } from "./admin/manage-curriculums/manage-curriculums.component";
import { StudentDashboardComponent } from "./student/student-dashboard/student-dashboard.component";

const routes: Routes = [
  {
    path: "",
    redirectTo: "school-admin/student",
    pathMatch: "full"
  },
  {
    path: "dashboard",
    component: DashboardComponent,
    data: { title: "Dashboard" },
    pathMatch: "full"
  },
  {
    path: "school-admin/student",
    component: StudentComponent,
    data: { title: "School Admin Student" },
    pathMatch: "full"
  },
  {
    path: "manage-curriculums",
    component: ManageCurriculumsComponent,
    data: { title: "Dashboard" },
    pathMatch: "full"
  },
  {
    path: "my-dashboard",
    component: StudentDashboardComponent,
    data: { title: "Student Dashboard" },
    pathMatch: "full"
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule {}
