import { Component, ViewChild } from "@angular/core";
import { MatDialogRef, MatDialog } from "@angular/material";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";
import { SubscribersDialogComponent } from "../subcribers-grid-dialog/subcribers-grid-dialog.component";

@Component({
  templateUrl: "./courses-list.component.html",
  styleUrls: ["./courses-list.component.scss"]
})
export class CoursesListComponent {
  subcribersDialogRef:MatDialogRef<SubscribersDialogComponent>;

  constructor(public dialog: MatDialog, private routes: Router) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(60vh)";
  }

  data: any[] = [
    {
      title: "Telugu Alphabets",
      publishedby: "Ganesh",
      objective: "To understand a telugu grammer ..",
      publishedon: "31/08/18",
      grades: "6,8,9",
      concepts: "Number System"
    },
    {
      title: "How to make a sentence",
      publishedby: "Ganesh",
      objective: "To understand a telugu ..",
      publishedon: "31/08/18",
      grades: "6,8,9",
      concepts: "Number System"
    },
    {
      title: "How to make a words",
      publishedby: "Ganesh",
      objective: "To understand a telugu ..",
      publishedon: "31/08/18",
      grades: "6,8,9",
      concepts: "Number System"
    },
    {
      title: "Telugu vocabularies",
      publishedby: "Ganesh",
      objective: "To understand a telugu ..",
      publishedon: "31/08/18",
      grades: "6,8,9",
      concepts: "Number System"
    },
  ];
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "title", type: "string" },
      { name: "objective", type: "string" },
      { name: "concepts", type: "string" },
      { name: "publishedon", type: "date" },
      { name: "publishedby", type: "string" },
      { name: "grades", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Title",
      dataField: "title",
      align: "center",
      cellsAlign: "center",
      width: 174
    },
    {
      text: "Objective",
      dataField: "objective",
      align: "center",
      cellsAlign: "center",
      width: 174
    },
    {
      text: "Pre-requisite Concepts",
      dataField: "concepts",
      align: "center",
      cellsAlign: "center",
      width: 174
    },
    {
      text: "Grades Applicable",
      align: "center",
      cellsAlign: "center",
      dataField: "grades",
      width: 174
    },
    {
      text: "Published by",
      align: "center",
      cellsAlign: "center",
      dataField: "publishedby",
      width: 174
    },
    {
      text: "Published on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "publishedon",
      width: 174
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      width: 300,
      columnType: "none",
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `<div data-row='` +
          row +
          `' class='viewButton' style='color:white;background-color:indigo;float:left;margin-left: 18px;'></div></div><div data-row='` +
          row +
          `' class='subscribersButton' style='color:white;background-color:green;float:left;'></div><div data-row='` +
          row +
          `' class='assignButton' style='color:white;background-color:#d81b60;float:left;'>`
        );
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyassignButtons = jqwidgets.createInstance(
      ".assignButton",
      "jqxButton",
      {
        width: 80,
        height: 24,
        value: "Assign"
      }
    );
    let uglyviewButtons = jqwidgets.createInstance(".viewButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "View"
    });
    let uglysubscribersButtons = jqwidgets.createInstance(
      ".subscribersButton",
      "jqxButton",
      {
        width: 120,
        height: 24,
        value: "View Subscribers"
      }
    );
    let flattenviewButtons = flatten(uglyviewButtons);
    let flattenassignButtons = flatten(uglyassignButtons);
    let flattensubscribersButtons = flatten(uglysubscribersButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }
    if (flattenviewButtons) {
      for (let i = 0; i < flattenviewButtons.length; i++) {
        flattenviewButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattenassignButtons) {
      for (let i = 0; i < flattenassignButtons.length; i++) {
        flattenassignButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattensubscribersButtons) {
      for (let i = 0; i < flattensubscribersButtons.length; i++) {
        flattensubscribersButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
    if (value === "View Subscribers") {
      this.openSubscribersDialog();
    }
    if (value === "Assign"){
      this.openAssigneesList();
    }
  }
  openSubscribersDialog(){
    this.subcribersDialogRef = this.dialog.open(
      SubscribersDialogComponent,
      {
        width: "60%",
        height: "calc(50vh)"
      }
    );
    this.subcribersDialogRef.disableClose = true;
  }

  openAssigneesList(){
    this.routes.navigateByUrl('student-courses/assignees-list');
  }

  onBack() {
    this.routes.navigateByUrl("/student-courses");
  }
}
