import { Component, ComponentFactoryResolver, ViewChild, ViewContainerRef, ComponentRef } from "@angular/core";
import { Router } from "@angular/router";
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl
} from "@angular/forms";
import { ActivityPanelComponent } from "../activity-panel/activity-panel.component";

@Component({
  selector: "create-project-activity",
  templateUrl: "./create-project-activity.component.html",
  styleUrls: ["./create-project-activity.component.scss"]
})
export class CreateProjectActivityComponent {
  showFileUpload: boolean;
  showWebURL: boolean;
  resourceTypevalue: string;
  activityForm: FormGroup;
  activityTitle: string = "";
  objective: string = "";

  @ViewChild("activityPanel", { read: ViewContainerRef })
  activityPanel: ViewContainerRef;
  index: number = 0;
  compInteraction
  componentsReferences = [];

  constructor(private routes: Router, private formBuilder: FormBuilder,private componentFactoryResolver: ComponentFactoryResolver) {}

  ngOnInit() {
    this.addactivityPanel();
    this.activityForm = this.formBuilder.group({
      activityTitle: new FormControl("", Validators.required),
      objective: new FormControl("", Validators.required),
    });
  }

  saveContent() {
    this.activityTitle = this.activityForm.value.activityTitle;
    this.objective = this.activityForm.value.objective;
    if (
      this.activityTitle !== "" &&
      this.objective !== "" &&
      this.activityTitle !== null &&
      this.objective !== null 
    ) {
      this.activityForm.reset();
    } else {
      alert("Please fill the required fields");
    }
  }


  addactivityPanel() {

    let componentFactory = this.componentFactoryResolver.resolveComponentFactory(ActivityPanelComponent);
    let componentRef: ComponentRef<ActivityPanelComponent> = this.activityPanel.createComponent(componentFactory);
    let currentComponent = componentRef.instance;

    currentComponent.index = ++this.index;

    currentComponent.compInteraction = this;

    this.componentsReferences.push(componentRef);
  }

  remove(index: number) {

    if (this.activityPanel.length < 1)
      return;

    let componentRef = this.componentsReferences.filter(x => x.instance.index == index)[0];

    let sectionIndex: number = this.activityPanel.indexOf(componentRef)

    this.activityPanel.remove(sectionIndex);

    this.componentsReferences = this.componentsReferences.filter(x => x.instance.index !== index);
  }


}
