import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { StudentComponent } from "./student/student.component";
import { TeacherComponent } from "./teacher/teacher.component";
import { ClassTeacherComponent } from "./class-teacher/class-teacher.component";
import { PrincipalComponent } from "./principal/principal.component";
import { ContentAdminComponent } from "./content-admin/content-admin.component";
import { UserAdminComponent } from "./user-admin/user-admin.component";
import { CreateStudentComponent } from "./student/create-student/create-student.component";
import { CreateClassTeacherComponent } from "./class-teacher/create-class-teacher/create-class-teacher.component";
import { CreateTeacherComponent } from "./teacher/create-teacher/create-teacher.component";
import { CreatePrincipalComponent } from "./principal/create-principal/create-principal.component";
import { CreateContentAdminComponent } from "./content-admin/create-content-admin/create-content-admin.component";
import { CreateUserAdminComponent } from "./user-admin/create-user-admin/create-user-admin.component";
const routes: Routes = [
  {
    path: "school-user-admin/student",
    component: StudentComponent,
    data: { title: "Student" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/student/create-new",
    component: CreateStudentComponent,
    data: { title: "Create New Student" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/teacher",
    component: TeacherComponent,
    data: { title: "Teacher" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/teacher/create-new",
    component: CreateTeacherComponent,
    data: { title: "Create New Teacher" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/class-teacher",
    component: ClassTeacherComponent,
    data: { title: "Class Teacher" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/class-teacher/create-new",
    component: CreateClassTeacherComponent,
    data: { title: "Class Teacher" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/principal",
    component: PrincipalComponent,
    data: { title: "Principal" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/principal/create-new",
    component: CreatePrincipalComponent,
    data: { title: "Create New Principal" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/content-admin",
    component: ContentAdminComponent,
    data: { title: "Content Admin" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/content-admin/create-new",
    component: CreateContentAdminComponent,
    data: { title: "Create New Content Admin" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/user-admin",
    component: UserAdminComponent,
    data: { title: "User Admin" },
    pathMatch: "full"
  },
  {
    path: "school-user-admin/user-admin/create-new",
    component: CreateUserAdminComponent,
    data: { title: "Create User Admin" },
    pathMatch: "full"
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class SchoolAdminRoutesModule {}
