import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtree";

export interface DataSource {
  orderno: number;
  name: string;
  contentType: string;
  contentCategory: string;
  icon: string;
  addedon: string;
  addedby: string;
}

@Component({
  templateUrl: "manage-other-learning-resources.component.html",
  styleUrls: ["manage-other-learning-resources.component.scss"]
})
export class ManageOtherLearningResourcesComponent {
  showScroll1: boolean;
  showAddNewContent: boolean;
  showEditContent: boolean;
  showBasicContent: boolean;
  showVideo: boolean;
  showDocument: boolean;
  lessonTitle: string = "";
  lessonSource: any[] = [
    {
      label: "Maths",
      expanded: true,
      items: [
        {
          label: "Real Numbers",
          expanded: true,
          items: [
            {
              label: "Introduction"
            },
            {
              label: "Rational Numbers"
            }
          ]
        },
        {
          label: "Sets",
          expanded: true,
          items: [
            {
              label: "Introduction"
            },
            {
              label: "Venn Diagrams"
            }
          ]
        },
        {
          label: "Algebra",
          expanded: true,
          items: [
            {
              label: "Basic Fundamentals"
            },
            {
              label: "Introduction"
            }
          ]
        }
      ]
    }
  ];

  showPrequisiteStandard: boolean;

  @ViewChild("treeReference")
  treeReference: jqxTreeComponent;

  constructor(private routes: Router) {}

  sources: DataSource[] = [
    {
      orderno: 1,
      name: "Real Numbers - Introduction",
      contentCategory: "Video",
      contentType: "File Upload",
      icon: "play_circle_filled",
      addedby: "Rajendra",
      addedon: "10/12/18"
    },
    {
      orderno: 2,
      name: "Square Numbers",
      contentCategory: "Document",
      contentType: "Web Source",
      icon: "insert_drive_file",
      addedby: "Ganesh",
      addedon: "10/12/18"
    },
    {
      orderno: 3,
      name: "Cube Numbers",
      contentCategory: "Document",
      contentType: "File Upload",
      icon: "insert_drive_file",
      addedby: "Rajendra",
      addedon: "10/12/18"
    },
    {
      orderno: 4,
      name: "Rational Numbers",
      contentCategory: "Video",
      contentType: "Web Source",
      icon: "play_circle_filled",
      addedby: "Rajendra",
      addedon: "10/12/18"
    }
  ];

  myTreePreviewSelect(event: any): void {
    let args = event.args;
    let item = this.treeReference.getItem(args.element);
    this.lessonTitle = item.label;
    if (item.hasItems == false) {
      this.showBasicContent = true;
      this.showAddNewContent = false;
      this.showEditContent = false;
    } else {
      this.showBasicContent = false;
      this.showAddNewContent = false;
      this.showEditContent = false;
    }
  }

  openNewContent() {
    this.showAddNewContent = true;
    this.showBasicContent = false;
    this.showEditContent = false;
  }
  openEditContent() {
    this.showEditContent = true;
    this.showAddNewContent = false;
    this.showBasicContent = false;
  }
  onBack() {
    this.showBasicContent = true;
    this.showAddNewContent = false;
    this.showEditContent = false;
  }
}
