import { CommunicationListComponent } from './communication/communication-list/communication-list.component';
import { LearningPathsListComponent } from './learning-path/learning-paths-list/learning-paths-list.component';
import { NgModule } from '@angular/core';
import { PerformanceSummaryListComponent } from './peformance-summary/performance-summary-list/performance-summary-list.component';
import { RouterModule, Routes } from '@angular/router';
import { LessonSummaryListComponent } from './lesson-summary/lesson-summary-list/lesson-summary-list.component';
import { PerformanceSummaryListCurriculumComponent } from './peformance-summary/performance-summary-list-curriculum/performance-summary-list-curriculum.component';
import { PerformanceSummaryListAdhocComponent } from './peformance-summary/performance-summary-list-adhoc/performance-summary-list-adhoc.component';
import { LearningPathsListCurriculumComponent } from './learning-path/learning-paths-list-curriculum/learning-paths-list-curriculum.component';
import { CommunicationNewComponent } from './communication/communication-new/communication-new.component';

const routes: Routes = [
  {
    path: "classroom/performance-summary-list/prerequisite-assessment",
    component: PerformanceSummaryListComponent,
    data: { title: "Performance Summary List" },
    pathMatch: "full"
  },
  {
    path: "classroom/performance-summary-list/curriculum-assessment",
    component: PerformanceSummaryListCurriculumComponent,
    data: { title: "Performance Summary List" },
    pathMatch: "full"
  },
  {
    path: "classroom/performance-summary-list/adhoc-assessment",
    component: PerformanceSummaryListAdhocComponent,
    data: { title: "Performance Summary List" },
    pathMatch: "full"
  },
  {
    path: "classroom/lesson-progress-summary-list",
    component: LessonSummaryListComponent,
    data: { title: "Lesson Progress Summary List" },
    pathMatch: "full"
  },
  {
    path: "classroom/learning-paths-list/prerequisite-standard",
    component: LearningPathsListComponent,
    data: { title: "Learning Paths List" },
    pathMatch: "full"
  },
  {
    path: "classroom/learning-paths-list/curriculum-lesson",
    component: LearningPathsListCurriculumComponent,
    data: { title: "Learning Paths List" },
    pathMatch: "full"
  },
  {
    path: "classroom/communication",
    component: CommunicationListComponent,
    data: { title: "Communication List" },
    pathMatch: "full"
  },
  {
    path: "classroom/communication/create-new",
    component: CommunicationNewComponent,
    data: { title: "Communication New" },
    pathMatch: "full"
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ClassroomDashboardRoutesModule {}
