import { Component } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MatDialogRef } from "@angular/material";
import { Router } from "@angular/router";

export interface Field {
  value: string;
  viewValue: string;
}
@Component({
  selector: "subject-view",
  templateUrl: "./subject-view.component.html",
  styleUrls: ["./subject-view.component.scss"]
})
export class SubjectViewComponent {
  students = new FormControl();
  showInfo: boolean;
  curriculum: Field[] = [
    { value: "cbse", viewValue: "CBSE" },
    { value: "ts", viewValue: "Telangana" },
    { value: "tn", viewValue: "Tamilnadu" }
  ];

  grades: Field[] = [
    { value: "1", viewValue: "1" },
    { value: "2", viewValue: "2" },
    { value: "3", viewValue: "3" },
    { value: "4", viewValue: "4" },
    { value: "5", viewValue: "5" },
    { value: "6", viewValue: "6" }
  ];

  subjects: Field[] = [
    { value: "Telugu", viewValue: "Telugu" },
    { value: "English", viewValue: "English" },
    { value: "Physics", viewValue: "Physics" },
    { value: "Chemistry", viewValue: "Chemistry" },
    { value: "Mathematics", viewValue: "Mathematics" },
    { value: "History", viewValue: "History" }
  ];

  constructor(private routes: Router) {}
  Edit() {}
  additionalInfo() {
    this.showInfo = true;
  }
  onBack() {
    this.routes.navigateByUrl("/manage-subjects");
  }
}
