import { Component } from "@angular/core";
import { Router } from "@angular/router";

@Component({
  templateUrl: "content-library.component.html",
  styleUrls: ["content-library.component.scss"]
})
export class ContentLibraryComponent {
  showScroll1:boolean;
  showScroll2:boolean;
  showScroll3:boolean;
  showScroll4:boolean;

  constructor(private routes:Router){}

  openCurriculumContent(){
    this.routes.navigateByUrl('/content-library/dashboard/curriculum-content');
  }
  openPrerequisiteContent(){
    this.routes.navigateByUrl('/content-library/dashboard/prerequisite-standard');
  }
  openProjectContent(){
    this.routes.navigateByUrl('/content-library/dashboard/project-content');
  }
  openLearningResources(){
    this.routes.navigateByUrl('/content-library/dashboard/other-learning-resources');
  }
}
