import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";
import { MatDialog, MatDialogRef } from "@angular/material";
import { PublishCurriculumComponent } from "./publish-curriculum/publish-curriculum.component";

@Component({
  selector: "manage-curriculums",
  templateUrl: "./manage-curriculums.component.html",
  styleUrls: ["./manage-curriculums.component.scss"]
})
export class ManageCurriculumsComponent {
  dialogRef: MatDialogRef<PublishCurriculumComponent>;
  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(70vh)";
  }
  constructor(private routes: Router, private dialog: MatDialog) {}

  data: any[] = [
    {
      curriculumname: "CBSE",
      country: "India",
      state: "Telangana",

      status: "draft"
    },
    {
      curriculumname: "Stateboard",
      country: "India",
      state: "Tamilnadu",

      status: "published"
    },
    {
      curriculumname: "CBSE",
      country: "India",
      state: "Telangana",

      status: "draft"
    }
  ];
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "curriculumname", type: "string" },
      { name: "country", type: "string" },
      { name: "state", type: "string" },
      { name: "status", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Curriculum Name",
      dataField: "curriculumname",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Country",
      dataField: "country",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "State",
      dataField: "state",
      align: "center",
      cellsAlign: "center",
      width: 108
    },
    {
      text: "Status",
      dataField: "status",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      columnType: "none",
      width: 198,
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (
        row: number,
        column: any,
        value: any,
        rowData
      ): string => {
        if (rowData.status !== "draft") {
          return (
            `<div data-row='` +
            row +
            `' class='viewButton' style='color:white;background-color:indigo;margin-left: 20px;float: left;margin-right: 5px;'></div><div data-row='` +
            row +
            `' class='deprecateButton' style='color:white;background-color:skyblue;margin:0 5px;float:left'></div><div data-row='` +
            row +
            `' class='deprecateButton' style='color:white;background-color:skyblue;display:none;margin-left: 20px;float: left;margin-right: 5px;'></div>`
          );
        } else {
          return (
            `<div data-row='` +
            row +
            `' class='viewButton' style='color:white;background-color:indigo;margin-left: 20px;float: left;margin-right: 5px;'></div><div data-row='` +
            row +
            `' class='publishButton' style='color:white;background-color:green;margin:0 5px;float:left'></div>`
          );
        }
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyviewButtons = jqwidgets.createInstance(".viewButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "View"
    });

    let uglydeprecateButtons = jqwidgets.createInstance(
      ".deprecateButton",
      "jqxButton",
      {
        width: 80,
        height: 24,
        value: "Deprecate"
      }
    );

    let uglypublishButtons = jqwidgets.createInstance(
      ".publishButton",
      "jqxButton",
      {
        width: 80,
        height: 24,
        value: "Publish"
      }
    );

    let flattenviewButtons = flatten(uglyviewButtons);
    let flattendeprecateButtons = flatten(uglydeprecateButtons);
    let flattenpublishButtons = flatten(uglypublishButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }
    if (flattenviewButtons) {
      for (let i = 0; i < flattenviewButtons.length; i++) {
        flattenviewButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }

    if (flattendeprecateButtons) {
      for (let i = 0; i < flattendeprecateButtons.length; i++) {
        flattendeprecateButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattenpublishButtons) {
      for (let i = 0; i < flattenpublishButtons.length; i++) {
        flattenpublishButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
    if (value === "View") {
      this.openViewCurriculum();
    }
    if (value === "Publish") {
      this.openPublish();
    }
    if (value === "Deprecate") {
      this.openDeprecate();
    }
  }

  openViewAffiliations() {
    this.routes.navigateByUrl("manage-curriculums/view-affiliations");
  }

  openPublish() {
    this.dialogRef = this.dialog.open(PublishCurriculumComponent, {
      width: "50%",
      height: "calc(88vh)"
    });
    this.dialogRef.componentInstance.showPublish = true;
    this.dialogRef.disableClose = true;
  }

  openDeprecate() {
    this.dialogRef = this.dialog.open(PublishCurriculumComponent, {
      width: "50%",
      height: "calc(88vh)"
    });
    this.dialogRef.componentInstance.showDeprecate = true;
    this.dialogRef.disableClose = true;
  }

  openNewCurriculum() {
    this.routes.navigateByUrl("manage-curriculums/create-new");
  }

  openViewCurriculum() {
    this.routes.navigateByUrl("/manage-curriculums/view-curriculum");
  }
}
