import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";
import { MatDialog, MatDialogRef } from "@angular/material";

@Component({
  selector: "class-teacher",
  templateUrl: "./class-teacher.component.html",
  styleUrls: ["../school-admin.component.scss"]
})
export class ClassTeacherComponent {
  constructor(private routes: Router, private dialog: MatDialog) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(70vh)";
  }

  data: any[] = [
    {
      name: "Rajendra",
      teacherinfo: "9.A.Mathematics",
      otherroles: "Teacher",
      assigned: "9-A",
      loginId: "rajendra123@hmal.com",
      status: "Active",
      createdon: "12/03/18"
    },
    {
      name: "Kiran",
      teacherinfo: "10.A.Science",
      otherroles: "Teacher",
      assigned: "10-A",
      loginId: "kiran246@hmal.com",
      status: "Inactive",
      createdon: "23/03/18"
    }
  ];
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "name", type: "string" },
      { name: "teacherinfo", type: "string" },
      { name: "otherroles", type: "string" },
      { name: "assigned", type: "string" },
      { name: "loginId", type: "string" },
      { name: "status", type: "string" },
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Name",
      dataField: "name",
      align: "center",
      cellsAlign: "center",
      width: 200
    },
    {
      text: "Login ID",
      dataField: "loginId",
      align: "center",
      cellsAlign: "center",
      width: 150
    },
    {
      text: "Classroom and Subject",
      dataField: "teacherinfo",
      align: "center",
      width: 200,
      cellsAlign: "center"
    },
    {
      text: "Other Roles",
      dataField: "otherroles",
      align: "center",
      width: 200,
      cellsAlign: "center"
    },
    {
      text: "Class Assigned",
      dataField: "assigned",
      align: "center",
      width: 200,
      cellsAlign: "center"
    },
    {
      text: "Status",
      dataField: "status",
      align: "center",
      cellsAlign: "center",
      width: 150
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      columnType: "none",
      width: 290,
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (
        row: number,
        column: any,
        value: any,
        rowData
      ): string => {
        if (rowData.status === "Inactive") {
          return (
            `<div data-row='` +
            row +
            `' class='viewButton' style='color:white;background-color:green;float:left;margin-left: 20px;'></div><div data-row='` +
            row +
            `' class='activateButton' style='color:white;background-color:orange;float:left;'></div><div data-row='` +
            row +
            `' class='resetButton' style='color:white;background-color:red;float:left;'></div>`
          );
          // "View Activate Edit Reset PWD";
        } else if (rowData.status === "Active") {
          return (
            `<div data-row='` +
            row +
            `' class='viewButton' style='color:white;background-color:green;float:left;margin-left: 10px;'></div><div data-row='` +
            row +
            `' class='deactivateButton' style='color:white;background-color:orange;float:left;'></div><div data-row='` +
            row +
            `' class='resetButton' style='color:white;background-color:red;float:left;'></div>`
          );
          // "View Deactivate Edit Reset Password ";
        }
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyviewButtons = jqwidgets.createInstance(".viewButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "View"
    });
    let uglyactivateButtons = jqwidgets.createInstance(
      ".activateButton",
      "jqxButton",
      {
        width: 60,
        height: 24,
        value: "Activate"
      }
    );
    let uglydeactivateButtons = jqwidgets.createInstance(
      ".deactivateButton",
      "jqxButton",
      {
        width: 80,
        height: 24,
        value: "Deactivate"
      }
    );
    let uglyresetButtons = jqwidgets.createInstance(
      ".resetButton",
      "jqxButton",
      {
        width: 120,
        height: 24,
        value: "Reset Password"
      }
    );

    let flattenviewButtons = flatten(uglyviewButtons);
    let flattenactivateButtons = flatten(uglyactivateButtons);
    let flattendeactivateButtons = flatten(uglydeactivateButtons);
    let flattenresetButtons = flatten(uglyresetButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }
    if (flattenviewButtons) {
      for (let i = 0; i < flattenviewButtons.length; i++) {
        flattenviewButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattenactivateButtons) {
      for (let i = 0; i < flattenactivateButtons.length; i++) {
        flattenactivateButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattendeactivateButtons) {
      for (let i = 0; i < flattendeactivateButtons.length; i++) {
        flattendeactivateButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
    if (flattenresetButtons) {
      for (let i = 0; i < flattenresetButtons.length; i++) {
        flattenresetButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
  }

  createClassTeacher() {
    this.routes.navigateByUrl("school-user-admin/class-teacher/create-new");
  }
  
}
