import { Component } from '@angular/core';
import { StudentCircularsDialogComponent } from './student-circulars-dialog/student-circulars-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Router } from '@angular/router';

@Component({
  selector: 'student-circulars',
  templateUrl: './student-circulars.component.html',
  styleUrls: ['../student-dashboard.component.scss']
})
export class StudentCircularsComponent {

  showScroll:boolean;
  circularsDialogRef: MatDialogRef<StudentCircularsDialogComponent>;
  constructor(public dialog: MatDialog , private routes:Router) { }

  openCircularList(){
    this.routes.navigateByUrl('/my-dashboard/circular-list');
  }
}
