import { Component, ViewChild } from "@angular/core";
import { MatDialog, MatDialogRef } from "@angular/material";
import { StudentCircularsDialogComponent } from "../student-circulars-dialog/student-circulars-dialog.component";
import { Router } from "@angular/router";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";

@Component({
  selector: "student-circulars-list",
  templateUrl: "./student-circulars-list.component.html",
  styleUrls: ["./student-circulars-list.component.scss"]
})
export class StudentCircularListComponent {
  circularsDialogRef: MatDialogRef<StudentCircularsDialogComponent>;
  constructor(public dialog: MatDialog, private routes: Router) {}

  
  @ViewChild  ("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(69vh)";
  }

  data: any[] = [
    {
      title: "Report Card Issue Day",
      description: " Parents are requested to come personally along with their child (in school uniform) to collect the report cards of their children for Half Yearly Examination on Report Card Issue Day i.e. on 27th October, 2018. ",
      publishedon: "18/10/2018"
    },
   
  ];
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "title", type: "string" },
      { name: "publishedon", type: "date" },
      { name: "description", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Title",
      dataField: "title",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Description",
      dataField: "description",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Published on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "publishedon"
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      columnType: "none",
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `<a href="https://drive.google.com/open?id=1tE3u8rkWD0rWlyIhHdmHDhSTTaGoCg47" target=_blank""><div data-row='` +
          row +
          `' class='viewButton' style='color:white;background-color:lightslategrey;margin-left: 76px;'></div></a>`
        );
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyviewButtons = jqwidgets.createInstance(".viewButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "View"
    });
    let flattenviewButtons = flatten(uglyviewButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }
    if (flattenviewButtons) {
      for (let i = 0; i < flattenviewButtons.length; i++) {
        flattenviewButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
    if (value === "View") {
     // this.openDialog();
    }
  }

  onBack() {
    this.routes.navigateByUrl("/my-dashboard");
  }
}
