import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { CoursesListComponent } from "./courses-list/courses-list.component";
import { AssigneesSelectionComponent } from "./assignees-selection/assignees-selection.component";

const routes: Routes = [
  {
    path: "student-courses/courses-list",
    component: CoursesListComponent,
    data: { title: "Courses List" },
    pathMatch: "full"
  },
  {
    path: "student-courses/assignees-list",
    component: AssigneesSelectionComponent,
    data: { title: "Assignees List" },
    pathMatch: "full"
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StudentCoursesRoutesModule {}
