import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { jqxTreeComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtree";
@Component({
  selector: "pre-requisite-mapping",
  templateUrl: "./pre-requisite-mapping.component.html",
  styleUrls: ["./pre-requisite-mapping.component.scss"]
})
export class PrerequisiteMappingComponent {

  lessonSource: any[] = [
    {
      label: "Maths",
      expanded: true,
      items: [
        {
          label: "Algebra",
          expanded: true,
          items: [
            {
              label: "Basic Fundamentals",
              selected: true
            },
            {
              label: "Introduction",
              selected: true
            }
          ]
        },
        {
          label: "Trignometry",
          expanded: true,
          items: [
            {
              label: "Basics",
              selected: true
            },
            {
              label: "Sine and Cosine degree",
              selected: true
            }
          ]
        }
      ]
    }
  ];

  prerequisiteLessonSource: any[] = [
    {
      label: "Maths",
      expanded: true,
      items: [
        {
          label: "Algebra",
          expanded: true,
          items: [
            {
              label: "Introduction",
              selected: true
            }
          ]
        },
        {
          label: "Trignometry",
          expanded: true,
          items: [
            {
              label: "Introduction",
              selected: true
            }
          ]
        }
      ]
    }
  ];

  showPrequisiteStandard:boolean;

  showLessonTree : boolean;

  showPrerequisiteTree: boolean;

  gradeSelected1 : string ;

  subjectSelected1 : string ;

  gradeSelected2 : string ;

  subjectSelected2 : string ;
  
  @ViewChild("treeReference")
  treeReference: jqxTreeComponent;

  constructor(private routes: Router) {}

  myTreePreviewSelect(event: any): void {
    let args = event.args;
    let item = this.treeReference.getItem(args.element);
  }

  myTreeOnInitialized(): void {
    this.showPrequisiteStandard = false;
    document.addEventListener("contextmenu", event => {
      event.preventDefault();
      this.treeReference.selectItem(event.target);
    });
  }

  lessonValueChange(){
    if(this.gradeSelected1 !== "" && this.subjectSelected1 !== ""){
      this.showLessonTree = true;
    } else {
      this.showLessonTree = false;
    }
  }

  prerequisiteValueChange(){
    if(this.gradeSelected2 !== "" && this.subjectSelected2 !== ""){
      this.showPrerequisiteTree = true;
    } else {
      this.showPrerequisiteTree = false;
    }
  }

  onBack() {
    this.routes.navigateByUrl("/manage-prerequisite-standard-mapping");
  }
}
