import { Component } from "@angular/core";
import { MatDialog, MatDialogRef } from "@angular/material";
import { Router } from "@angular/router";

@Component({
  selector: "Update-lesson-progress-summary",
  templateUrl: "./update-lesson-progress-summary.component.html",
  styleUrls: ["../../lesson-management.component.scss"]
})
export class UpdateLessonProgressSummaryComponent {

  plans: string[] = ["Real Numbers Activity based", "Polynomials Advanced"];

  constructor(public dialog: MatDialog, private routes: Router) {}
  
  openCreatePlan() {
    this.routes.navigateByUrl("/lesson/lesson-plans/create-new");
  }

}
