import { Component, ViewChild } from "@angular/core";
import { FormControl, NgModel, FormGroup, FormBuilder } from "@angular/forms";
import { Router } from "@angular/router";
import { MatChipInputEvent, MatOption } from "@angular/material";
import { ReplaySubject, Subject } from "rxjs";
import { takeUntil, take } from "rxjs/operators";

export interface Filter {
  disabled?: boolean;
  name: string;
  key: string;
}

@Component({
  selector: "communication-new",
  templateUrl: "./communication-new.component.html",
  styleUrls: ["./communication-new.component.scss"]
})
export class CommunicationNewComponent {
  searchUserForm: FormGroup;
  selected: boolean;
  messageValue: string;

  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;

  @ViewChild("allSelected") private allSelected: MatOption;

  private filters: Filter[] = [
    {
      name: "Aravind Raja",
      key: "1"
    },
    {
      name: "Abdul kalam",
      key: "2"
    },
    {
      name: "Bala kumar",
      key: "3"
    },
    {
      name: "Chaitanya Krishna",
      key: "4"
    },
    {
      name: "Daniel Hammer",
      key: "5"
    },
    {
      name: "Kiran Kumar",
      key: "6"
    }
  ];

  private _onDestroy = new Subject<void>();

  public filter: FormControl = new FormControl();
  public searchFilter: FormControl = new FormControl();
  public filteredItems: ReplaySubject<Filter[]> = new ReplaySubject<Filter[]>(
    1
  );

  constructor(public fb:FormBuilder,public routes: Router){}

  ngOnInit() {
    this.filter.setValue(this.filters[100]);
    this.filteredItems.next(this.filters.slice());

    this.searchFilter.valueChanges
      .pipe(takeUntil(this._onDestroy))
      .subscribe(() => {
        this.filterItems();
      });

    this.searchUserForm = this.fb.group({
      filter : new FormControl('')
    });
  }

  toggleAllSelection() {
    if (this.allSelected.selected) {
      this.searchUserForm.controls.filter.patchValue([
        ...this.filters.map(item => item.key), 0]);
    } else {
      this.searchUserForm.controls.filter.patchValue([]);
    }
  }

  private setInitialValue() {
    this.filteredItems.pipe(
      take(1),
      takeUntil(this._onDestroy)
    );
  }

  private filterItems() {
    if (!this.filters) {
      return;
    }
    let search = this.searchFilter.value;
    if (!search) {
      this.filteredItems.next(this.filters.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    this.filteredItems.next(
      this.filters.filter(item => item.name.toLowerCase().indexOf(search) > -1)
    );
  }

  onChangeValue(radio) {
    let temp = radio.value;
  }

  onBack() {
    this.routes.navigateByUrl("/classroom/communication");
  }
}
