import { Component, ViewChild } from "@angular/core";
import { jqxTreeGridComponent } from "jqwidgets-scripts/jqwidgets-ts/angular_jqxtreegrid";
import { MatDialog, MatDialogRef } from "@angular/material";
import { StudentNewsDialogComponent } from "../student-news-dialog/student-news-dialog.component";
import { Router } from "@angular/router";

@Component({
  selector: "news-list",
  templateUrl: "./student-news-list.component.html",
  styleUrls: ["./student-news-list.component.scss"]
})
export class StudentNewsListComponent {
  newsDialogRef: MatDialogRef<StudentNewsDialogComponent>;
  constructor(public dialog: MatDialog, private routes: Router) {}

  @ViewChild("TreeGrid")
  treeGrid: jqxTreeGridComponent;

  getWidth(): any {
    return "100%";
  }

  getHeight(): any {
    return "calc(69vh)";
  }

  data: any[] = [
    {
      title:
        "US satellite startup that chose ISRO for launch is back online after defying US officials",
      description:
        "In January, Swarm Technologies Inc. placed four, tiny satellites on a rocket owned by the Indian government, sent them to space and started transmitting data to earth. What made this an unprecedented feat was that Swarm did all of it despite objections by US regulators. As far as anyone can remember, this was the first example of an American company placing a satellite into orbit without Uncle Sams blessing, and the results were as expected.",
      publishedon: "18/08/2018"
    },
    {
      title:
        "India masters rocket science: Heres why the new ISRO launch is special",
      description:
        "India on Wednesday, put a communication satellite in the orbit with its heaviest rocket and the new space mission is expected to serve the communication needs of people in the remote areas of the country.The rocket lifted off with a 3,423 kg GSAT-29 satellite, heaviest satellite in Indian history. Indian Space Research Organisation(ISRO) chief K Sivan said ,It is an important milestone for the Indian Space Programme towards achieving self-reliance in launching heavier satellites.The Geosynchronous Satellite Launch Vehicle (GSLV-Mk III) is Indias most advanced and powerful rocket.      With its second successful flight, GSLV Mk III will be declared operational.The big tech increment in GSLV is its cryogenic engine, which uses liquefied gases to extract powerful thrust, and its S200 solid core engine which is the worlds third largest.  It was a cryogenic engine that powered Nasas Saturn V rocket, which took Neil Armstrong and Buzz Aldrin to the moon.  The GSLV Mk-III will be used as the launch vehicle for Indias first spaceflight programme scheduled for 2022.   The launch took place amid a threat of worsening weather conditions, due to Cyclone Gaja. But the cyclone changed course, allowing the launch to go ahead.",
      publishedon: "18/08/2018"
    },
    {
      title:
        "US satellite startup that chose ISRO for launch is back online after defying US officials",
      description:
        "In January, Swarm Technologies Inc. placed four, tiny satellites on a rocket owned by the Indian government, sent them to space and started transmitting data to earth. What made this an unprecedented feat was that Swarm did all of it despite objections by US regulators. As far as anyone can remember, this was the first example of an American company placing a satellite into orbit without Uncle Sams blessing, and the results were as expected.",
      publishedon: "18/08/2018"
    }
  ];
  source: any = {
    dataType: "json",
    dataFields: [
      { name: "title", type: "string" },
      { name: "publishedon", type: "date" },
      { name: "description", type: "string" }
    ],
    localData: this.data,
    id: "id"
  };
  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] = [
    {
      text: "Title",
      dataField: "title",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Description",
      dataField: "description",
      align: "center",
      cellsAlign: "center"
    },
    {
      text: "Published on",
      align: "center",
      cellsAlign: "center",
      cellsFormat: "d",
      dataField: "publishedon"
    },
    {
      text: "Actions",
      cellsAlign: "center",
      align: "center",
      columnType: "none",
      editable: false,
      sortable: false,
      dataField: null,
      cellsRenderer: (row: number, column: any, value: any): string => {
        return (
          `<div data-row='` +
          row +
          `' class='viewButton' style='color:white;background-color:lightslategrey;margin-left: 76px;'></div>`
        );
      }
    }
  ];
  editSettings: any = {
    saveOnPageChange: true,
    saveOnBlur: true,
    saveOnSelectionChange: false,
    cancelOnEsc: true,
    saveOnEnter: true,
    editOnDoubleClick: false,
    editOnF2: false
  };
  rendered = (): void => {
    let uglyviewButtons = jqwidgets.createInstance(".viewButton", "jqxButton", {
      width: 60,
      height: 24,
      value: "View"
    });
    let flattenviewButtons = flatten(uglyviewButtons);

    function flatten(arr: any[]): any[] {
      if (arr.length) {
        return arr.reduce((flat: any[], toFlatten: any[]): any[] => {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }
    }
    if (flattenviewButtons) {
      for (let i = 0; i < flattenviewButtons.length; i++) {
        flattenviewButtons[i].addEventHandler(
          "click",
          (event: any): void => {
            this.editClick(event);
          }
        );
      }
    }
  };

  rowKey: number = -1;
  cellClick(event: any): void {
    this.rowKey = event.args.key;
  }
  editClick(event: any): void {
    let value = event.target.innerText;
    if (value === "View") {
      this.openDialog();
    }
  }

  public openDialog() {
    this.newsDialogRef = this.dialog.open(StudentNewsDialogComponent, {
      width: "50%",
      height: "calc(35vh)"
    });
    this.newsDialogRef.disableClose = true;
  }

  onBack() {
    this.routes.navigateByUrl("/my-dashboard");
  }
}
