import { Component } from "@angular/core";
import { Router } from "@angular/router";
import {
  FormGroup,
  FormControl,
  Validators,
  FormBuilder
} from "@angular/forms";

export interface Filter {
  disabled?: boolean;
  name: string;
  value: string;
}

@Component({
  selector: "create-principal",
  templateUrl: "./create-principal.component.html",
  styleUrls: ["../../school-admin.component.scss"]
})
export class CreatePrincipalComponent {
  isLinear = false;

  showTeacher: boolean;

  showClassTeacher: boolean;

  showContentAdmin: boolean;

  firstFormGroup: FormGroup;

  secondFormGroup: FormGroup;

  thirdFormGroup: FormGroup;

  fourthFormGroup: FormGroup;

  step = 0;

  value: string;

  private grades: Filter[] = [
    {
      name: "7",
      value: "7"
    },
    {
      name: "8",
      value: "8"
    },
    {
      name: "9",
      value: "9"
    },
    {
      name: "10",
      value: "10"
    }
  ];

  private sections: Filter[] = [
    {
      name: "A",
      value: "1"
    },
    {
      name: "B",
      value: "2"
    },
    {
      name: "C",
      value: "3"
    },
    {
      name: "D",
      value: "4"
    }
  ];

  private subjects: Filter[] = [
    {
      name: "English",
      value: "1"
    },
    {
      name: "Mathematics",
      value: "2"
    },
    {
      name: "Science",
      value: "3"
    },
    {
      name: "Social Science",
      value: "4"
    }
  ];

  teacherGradeSelected: string = "";

  teacherSubjectSelected: string = "";

  teacherSectionSelected: string = "";

  classTeacherGradeSelected: string = "";

  classTeacherSectionSelected: string = "";

  contentAdminSubjectSelected: string = "";

  showTeacherData: boolean;

  showClassTeacherData: boolean;

  showContentAdminData: boolean;

  selectedValues = [];

  showTeacherDelete: boolean;

  showClassTeacherDelete: boolean;

  showContentAdminDelete: boolean;

  constructor(private _formBuilder: FormBuilder, private routes: Router) {}

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      //  firstName: new FormControl(["", Validators.required]),
      //  middleName: new FormControl(["", Validators.required]),
      //  lastName: new FormControl(["", Validators.required]),
    });
    this.secondFormGroup = this._formBuilder.group({});
    this.thirdFormGroup = this._formBuilder.group({
      student: new FormControl(["", Validators.required])
    });
    this.fourthFormGroup = this._formBuilder.group({});
  }

  setStep(index: number) {
    this.step = index;
  }

  nextStep() {
    this.step++;
  }

  prevStep() {
    this.step--;
  }

  teacherSelected(event) {
    if (event.checked === true) {
      this.showTeacher = true;
    } else {
      this.showTeacher = false;
    }
  }

  classTeacherSelected(event) {
    if (event.checked === true) {
      this.showClassTeacher = true;
    } else {
      this.showClassTeacher = false;
    }
  }

  contentAdminSelected(event) {
    if (event.checked === true) {
      this.showContentAdmin = true;
    } else {
      this.showContentAdmin = false;
    }
  }

  onTeacherData() {
    if (
      this.teacherGradeSelected !== "" &&
      this.teacherGradeSelected !== null &&
      (this.teacherSectionSelected !== "" &&
        this.teacherSectionSelected !== null) &&
      (this.teacherSubjectSelected !== "" &&
        this.teacherSubjectSelected !== null)
    ) {
      // this.selectedValues.push(this.gradeSelected,this.sectionSelected,this.subjectSelected);
      // console.log(this.selectedValues);
      this.showTeacherDelete = true;
      this.showTeacherData = true;
    } else {
      this.showTeacherData = false;
      this.showTeacherDelete = false;
    }
  }

  onClassTeacherData() {
    if (
      this.classTeacherGradeSelected !== "" &&
      this.classTeacherGradeSelected !== null &&
      (this.classTeacherSectionSelected !== "" &&
        this.classTeacherSectionSelected !== null)
    ) {
      // this.selectedValues.push(this.gradeSelected,this.sectionSelected,this.subjectSelected);
      // console.log(this.selectedValues);
      this.showClassTeacherDelete = true;
      this.showClassTeacherData = true;
    } else {
      this.showClassTeacherData = false;
      this.showClassTeacherDelete = false;
    }
  }

  onContentAdminData() {
    if (
      this.contentAdminSubjectSelected !== "" &&
      this.contentAdminSubjectSelected !== null
    ) {
      // this.selectedValues.push(this.gradeSelected,this.sectionSelected,this.subjectSelected);
      // console.log(this.selectedValues);
      this.showContentAdminData = true;
      this.showContentAdminDelete = true;
    } else {
      this.showContentAdminData = false;
      this.showContentAdminDelete = false;
    }
  }
  deleteTeacherData() {
    this.teacherGradeSelected = "";
    this.teacherSectionSelected = "";
    this.teacherSubjectSelected = "";
    this.showTeacherDelete = false;
  }
  deleteClassTeacherData() {
    this.classTeacherGradeSelected = "";
    this.classTeacherSectionSelected = "";
    this.showClassTeacherDelete = false;
  }
  deleteContentAdminData() {
    this.contentAdminSubjectSelected = "";
    this.showContentAdminDelete = false;
  }
  onBack() {
    this.routes.navigateByUrl("/school-user-admin/principal");
  }
}
